import { PRINT_REDUCER_EVENT, SET_PRODUCT_DISABLED_DOMAIN_LIST } from '../constant';

export default function productDisabledDomainListReducer (state = {
  list: []
}, action) {
  switch (action.type) {
    case SET_PRODUCT_DISABLED_DOMAIN_LIST:
      return {
        list: action.value
      };
    default:
      return {
        ...state
      };
  }
}
