import { SET_SELLER_DATA, CLEAR_SELLER_DATA, PRINT_REDUCER_EVENT } from '../constant';

export default function aliSellerDataReducer (state = {
  sellerData: {}
}, action) {
  switch (action.type) {
    case SET_SELLER_DATA:
      return {
        sellerData: {
          ...state.salerData,
          ...action.value
        },
      };
    case CLEAR_SELLER_DATA:
      return {
        sellerData: {},
      };
    default:
      return {
        ...state
      };
  }
}
