import { SET_ONBOARDING_SHOW, GET_ONBOARDING_SHOW, PRINT_REDUCER_EVENT } from '../constant';
import chromeLib from '../../lib/BrowserApi';

export class Onboarding {
    constructor(state) {
        this.data = state;
    }

    storageGet(name) {
        if (name === 'DelayMainOnboarding') {
            return chromeLib.storageGet(name)
        } else {
            return chromeLib.storageGet(name, 'to_show')
        }
    }

    // есть ли активное ожидание
    issetActiveDelay() {

        var storageDelay = this.storageGet("DelayMainOnboarding");

        if (storageDelay === undefined) {
            return false;
        }

        var delayDate = new Date(this.storageGet("DelayMainOnboarding").valueOf()),
            now = new Date().valueOf(),
            delay = 1000 * 60 * 60 * 24;


        return now - delayDate < delay;
    }

    updateAll() {

        var onboardings = [
            'MainOnboarding',
            'ProductCollectionOnboarding',
            'ProductPricesOnboarding',
            'ProductReviewsOnboarding',
            'ShopActivateOnboarding',
            'ShopReviewsOnboarding',
            'ShopPromocodesOnboarding',
            'UserBalanceOnboarding',
            'UserLoyaltyOnboarding',
        ];

        onboardings.map((item, i) => {
            this.data[item] = this.checkPriority(item);
        })

        return this.data;
    }

    checkPriority(name) {
        var main = this.storageGet("MainOnboarding"), // показываем основной
            issetDelay = this.issetActiveDelay(), // отложенный
            current = this.storageGet(name)


        if (issetDelay)
            return false;

        if (main === 'to_show' && name !== "MainOnboarding")
            return false;

        switch (name) {
            case "MainOnboarding": // 1 - priority
                return main === 'to_show';

            // User page
            case "UserBalanceOnboarding": // 2
                return current === 'to_show';

            case "UserLoyaltyOnboarding": // 3
                return this.storageGet("UserBalanceOnboarding") === 'shown'
                    && current === 'to_show';


            // Product page
            case "ProductCollectionOnboarding": // 2
                return current === 'to_show';
            case "ProductPricesOnboarding": // 3
                return this.storageGet("ProductCollectionOnboarding") === 'shown'
                    && current === 'to_show';
            case "ProductReviewsOnboarding": // 4
                return this.storageGet("ProductCollectionOnboarding") === 'shown'
                    && this.storageGet("ProductPricesOnboarding") === 'shown'
                    && current === 'to_show';


            // Shop page
            case "ShopActivateOnboarding": // 2
                return current === 'to_show';
            case "ShopReviewsOnboarding": // 3
                return this.storageGet("ShopActivateOnboarding") === 'shown'
                    && current === 'to_show';
            case "ShopPromocodesOnboarding": // 4
                return this.storageGet("ShopActivateOnboarding") === 'shown'
                    && this.storageGet("ShopReviewsOnboarding") === 'shown'
                    && current === 'to_show';
            default:
                break;

        }

        return true;
    }
}

export default function onboardingReducer(state = {
    MainOnboarding: chromeLib.storageGet("MainOnboarding", true),
    DelayMainOnboarding: chromeLib.storageGet("DelayMainOnboarding", null),
    ProductCollectionOnboarding: chromeLib.storageGet("ProductCollectionOnboarding", false),
    ProductPricesOnboarding: chromeLib.storageGet("ProductPricesOnboarding", false),
    ProductReviewsOnboarding: chromeLib.storageGet("ProductReviewsOnboarding", false),
    ShopActivateOnboarding: chromeLib.storageGet("ShopActivateOnboarding", false),
    ShopPromocodesOnboarding: chromeLib.storageGet("ShopPromocodesOnboarding", false),
    ShopReviewsOnboarding: chromeLib.storageGet("ShopReviewsOnboarding", false),
    UserBalanceOnboarding: chromeLib.storageGet("UserBalanceOnboarding", false),
    UserLoyaltyOnboarding: chromeLib.storageGet("UserLoyaltyOnboarding", false),
}, action) {
    switch (action.type) {
        case SET_ONBOARDING_SHOW:
            var value = (action.value === false) ? 'shown' : action.value;

            chromeLib.storageSet(action.name, value); // set false

            // чтобы обновить все
            var onboarding = new Onboarding(state);
            state = onboarding.updateAll();


            return {
                ...state
            };


        case GET_ONBOARDING_SHOW:

            var onboarding = new Onboarding(state);

            if (JSON.stringify(onboarding.updateAll()) === JSON.stringify(state)) {
                return state
            }

            return {
                ...onboarding.updateAll()
            };

        default:
            return {
                ...state
            };
    }
}
