import {
    SET_SHOW_TOPLINE_SETTINGS_MODAL,
    SET_SHOW_TOPLINE_POPUP_REMIND_CASHBACK_ACTIVATION,
} from '../constant';

export default function toplineReducer(state = {
    showSettingsModal: false,
    settingsModalPage: '',
    showPopupRemindActivation: false,
}, action) {
    switch (action.type) {
        case SET_SHOW_TOPLINE_SETTINGS_MODAL:
            return {...state, showSettingsModal: action.value, settingsModalPage: action.page};
        case SET_SHOW_TOPLINE_POPUP_REMIND_CASHBACK_ACTIVATION:
            return {...state, showPopupRemindActivation: action.value}
        default:
            return {
                ...state
            };
    }
}
