import { PRINT_REDUCER_EVENT, SET_ACTIVE_OFFER, SET_PRELOADER_ACTIVE_OFFER } from '../constant';

export default function activeOfferReducer(state = {
    id: 0,
    activeCash: 0,
    preloader: false,
}, action) {
    switch (action.type) {
        case SET_ACTIVE_OFFER:
            // console.log("activeOffer reducer :: SET_ACTIVE_OFFER - [prevState].activeCash [newState].activeCash and current time", state.activeCash, action.value.activeCash, Number((Date.now() / 1000).toFixed(0)));
            let offer_id = action.value !== undefined && action.value.id !== undefined ? action.value.id : 0;
            // let activeCash = action.value !== undefined && action.value.activeCash !== undefined ? action.value.activeCash : 0;
            let new_state = {
                ...action.value,
                // activeCash: activeCash,
                id: offer_id
            }
            // prevent re-render
            if (JSON.stringify(new_state) === JSON.stringify(state)) {
                return state
            }
            // console.log("activeOffer reducer :: SET_ACTIVE_OFFER - state CHANGED 2", new_state.activeCash);
            return new_state;

        case SET_PRELOADER_ACTIVE_OFFER:
            return {
                ...state,
                preloader: action.value,
            };
        default:
            return {
                ...state
            };
    }
}
