import { PRINT_REDUCER_EVENT, SET_POPULAR_OFFERS } from '../constant';

export default function popularOffersReducer (state = {
  offers: []
}, action) {
  switch (action.type) {
    case SET_POPULAR_OFFERS:
      return {
        offers: action.value,
      };
    default:
      return {
        ...state
      };
  }
}
