import chromeLib from './BrowserApi';
import helper from './Helper';
import {AUTO_REDIRECT, KEEP_ACTIVATED_AUTO} from "../background/actions";
import {MAX_COUNT_AUTO_REDIRECT, RESET_COUNT_AUTO_REDIRECT_TIME_AFTER} from "../background/constant";

class AutoRedirect {
    constructor() {
        this.offer = undefined;
        this.time = helper.getNowTimestamp() - 1;
        this.count = 0;
        this.activate_count = 0;
        // for aliexpress if auto_redirect activated
        this.is_activated_item_page = true;
        this.need_activate_item_page = false;
    }

    redirect(tab, offer, redirectType) {
        this.offer = offer;
        // console.log(offer, redirectType);
        this.redirectType = redirectType || AUTO_REDIRECT;

        this.getSavedData();
        try {
            return this.calcAutoRedirect(tab);
        } catch (e) {
            // console.log('Error AutoRedirect: ', e);
        }
        return false;
    }

    checkNeedAutoRedirect(tab) {
        // console.log('AutoRedirect::checkNeedAutoRedirect');
        let tmpAnswer = this.offer[AUTO_REDIRECT] !== undefined && this.offer[AUTO_REDIRECT] === "1";
        if (tmpAnswer) {
            this.redirectType = AUTO_REDIRECT;
            // aliexpress specific
            let urlToCheck = tab.pendingUrl ? tab.pendingUrl : tab.url;
            this.checkAutoActivatedOnItemPage(urlToCheck);
            // if aliexpress but not product page - do not auto-redirect
            if (!this.is_activated_item_page) {
                return false;
            }
        }
        // console.log('checkNeedAutoRedirect', tmpAnswer, this.count);
        return tmpAnswer;
    }

    checkNeedKeepActivatedAutoRedirect() {
        let tmpAnswer = this.offer[KEEP_ACTIVATED_AUTO] !== undefined
            && this.offer[KEEP_ACTIVATED_AUTO] === "1"
            && this.activate_count > 0
            && (this.offer.activeCash !== undefined && this.offer.activeCash > helper.getNowTimestamp());
        // console.log('AutoRedirect', this.count, this.offer, tmpAnswer )
        if (tmpAnswer) {
            this.redirectType = KEEP_ACTIVATED_AUTO;
        }
        return tmpAnswer;
    }

    increaseCount(url) {
        this.offer = offers.getOffer(url);
        this.activate_count++;
        this.save();
    }

    calcAutoRedirect(tab) {
        // console.log('___ calcAutoRedirect - this.need_activate_item_page', this.need_activate_item_page);

        if (this.offer === undefined) {
            console.warn("calcAutoRedirect -- Not found offer object");
            throw("Not found offer object");
        }

        if (this.offer.active !== "active") {
            console.warn("calcAutoRedirect -- Shop is not active");
            throw("Shop is not active");
        }

        if (helper.isActiveCashback(this.offer) && !this.need_activate_item_page) {
            console.warn("calcAutoRedirect -- Offer is already activate");
            throw("Offer is already activate");
        }

        if (this.offer.load_to_ext === undefined || this.offer.load_to_ext === "0" && !this.checkNeedKeepActivatedAutoRedirect()) {
            console.warn("calcAutoRedirect -- Shop is disabled in extension and KEEP_ACTIVATED_AUTO is disabled");
            throw("Shop is disabled in extension and KEEP_ACTIVATED_AUTO is disabled");
        }

        if (!this.checkNeedKeepActivatedAutoRedirect() && !this.checkNeedAutoRedirect(tab)) {
            console.warn("calcAutoRedirect -- Not need offer redirect");
            throw("Not need offer redirect");
        }

        if (this.isAliexpressAutoRedirectNotOnItemPage(this.getUrlFromTab(tab)) && this.need_activate_item_page) {
            console.warn("calcAutoRedirect -- Aliexpress with auto-redirect but not item page");
            throw("Aliexpress with auto-redirect but not item page");
        }

        if (MAX_COUNT_AUTO_REDIRECT <= this.count) {
            if (this.time <= helper.getNowTimestamp()) {
                throw("Max redirect count by this offer")
            }
            this.count = 0;
            this.activate_count = 0;
            this.save();
        }

        // console.log('AutoRedirect SUCCESS', this.checkNeedKeepActivatedAutoRedirect(), this.checkNeedAutoRedirect(tab))

        this.count++;
        this.save();

        this.autoActivate(tab);
        // api.userActivateClickLog(this.offer.id, this.redirectType);
        // console.log(helper.getUrlRedirect(this.offer, tab.url));
        // chromeLib.redirectTab(tab, helper.getUrlRedirect(this.offer, tab.url));
        return true;
    }

    storageRedirectKey() {
        var offer_id = this.offer ? this.offer.id : 0;
        return AUTO_REDIRECT.concat('_').concat(offer_id);
    }

    storageRedirectItemPageKey() {
        var offer_id = this.offer ? this.offer.id : 0;
        return "auto_redirect_item_page".concat('_').concat(offer_id);
    }

    getObjectVariable() {
        return "".concat(this.count)
            .concat("_")
            .concat(helper.getNowTimestamp() - 1);
    }

    getSavedData() {
        let variable = chromeLib.storageGet(this.storageRedirectKey(), this.getObjectVariable()).split('_');
        this.count = Number(variable[0]);
        this.time = Number(variable[1]);
    }

    getSavedAutoRedirectItemPage() {
        return chromeLib.storageGet(this.storageRedirectItemPageKey(), 0);
    }

    save() {
        this.time = helper.getNowTimestamp() + RESET_COUNT_AUTO_REDIRECT_TIME_AFTER;
        chromeLib.storageSet(this.storageRedirectKey(), this.getObjectVariable())
    }

    saveRedirectedItemPage(url) {
        // if auto_redirect on aliexpress - need to auto-redirect on item page
        // value = 1 - if activation was on item page that it, no actions required
        // value = 0 - if activation was NOT on item page - and auto-redirect on item page required
        let value = 1;
        if (this.offer === undefined) {
            this.offer = offers.getOffer(url);
        }
        if (this.isAliexpressAutoRedirectNotOnItemPage(url)) {
            value = 0;
        }
        chromeLib.storageSet(this.storageRedirectItemPageKey(), value);
    }

    isAliexpressAutoRedirectNotOnItemPage(url) {
        return this.offer[AUTO_REDIRECT] !== undefined
            && this.offer[AUTO_REDIRECT] === "1"
            && url !== ''
            && url.indexOf('aliexpress.') > -1
            && url.indexOf('/item/') === -1
    }

    checkAutoActivatedOnItemPage(url) {
        this.is_activated_item_page = !(url.indexOf('aliexpress.') > -1 && url.indexOf('/item/') === -1);
    }

    checkNeedAutoActivateOnItemPage(url) {
        this.saveRedirectedItemPage(url);
        this.need_activate_item_page = this.isAliexpressAutoRedirectNotOnItemPage(url) && (this.getSavedAutoRedirectItemPage() === 0);
        // console.log('___ checkNeedAutoActivateOnItemPage - this.need_activate_item_page', this.need_activate_item_page)
    }

    needAutoRedirectOnItemPage() {
        // console.log('___ needAutoRedirectOnItemPage - this.need_activate_item_page', this.need_activate_item_page)
        // if aliexpress with auto-redirect was previously activated NOT from item page
        if (this.need_activate_item_page) {
            return true;
        }
        return false;
    }

    getUrlFromTab(tab) {
        return tab.url ? tab.url : (tab.pendingUrl ? tab.pendingUrl : '');
    }

    autoActivate(tab) {
        helper.clickActivateCashback(this.offer, this.redirectType, tab);
        this.saveRedirectedItemPage(this.getUrlFromTab(tab));
        console.log('-- AUTO-ACTIVATED --', this.offer, this.redirectType, tab);
    }
}

const autoRedirect = new AutoRedirect();
export default autoRedirect;