import { PRINT_REDUCER_EVENT, SET_PRICE_HISTORY } from '../constant';

export default function priceHistoryReducer (state = {
  price_log:[],
  currency_symbol:'руб.'
}, action) {
  switch (action.type) {
    case SET_PRICE_HISTORY:
      return action.value || {};
    default:
      return {
        ...state
      };
  }
}
