import {
    SET_INCREASED_CASHBACK_POPUP_ALLOWED_DOMAINS,
    SET_INCREASED_CASHBACK_POPUP_DELAY,
    SET_INCREASED_CASHBACK_SHOPS
} from '../constant';

export default function increasedCashbackReducer(state = {
    domains: [],
    shops: [],
    delayAllDomains: 0
}, action) {
    switch (action.type) {
        case SET_INCREASED_CASHBACK_POPUP_ALLOWED_DOMAINS:
            return {
                ...state,
                domains: action.value
            };

        case SET_INCREASED_CASHBACK_SHOPS:
            return {
                ...state,
                shops: action.value
            };

        case SET_INCREASED_CASHBACK_POPUP_DELAY:

            let now = new Date().valueOf(),
                delay = 1000 * 60 * 60 * 24;

            return {
                ...state,
                delayAllDomains: now + delay
            };

        default:
            return {
                ...state
            };
    }
}
