import { CLEAR_CASHBACK_LINK, CREATE_CASHBACK_LINK, PRINT_REDUCER_EVENT } from '../constant';

export default function cashbackLinkReducer (state = {
  link: "",
  url: "",
}, action) {
  switch (action.type) {
    case CREATE_CASHBACK_LINK:
      return {
        ...state,
        link: action.link,
        url: action.url,
      };
    case CLEAR_CASHBACK_LINK:
      return {
        link: "",
        url: "",
      };
    default:
      return {
        ...state
      };
  }
}
