
class LinksRedirectsQueue {

    constructor() {
        // linksQueue's format: {"originalLink":"afterRedirectsLink",...} {"a.com":"b.com","c.com":"d.com",...};
        this.linksQueue = {};
        this.maxLength = 100;
    }

    addLink(linkKey, linkValue) {
        if ( !(linkKey in this.linksQueue) ) {
            this.linksQueue[linkKey] = linkValue;
            this.deleteRandomElementInQueue();
        }
    }

    getLinkValueByKey(linkKey) {
        if (linkKey in this.linksQueue) {
            return this.linksQueue[linkKey];
        }
        return null;
    }

    deleteRandomElementInQueue() {
        let randomIndex = Math.floor(Math.random() * Math.floor(this.maxLength)) + 1;
        delete this.linksQueue[Object.keys(this.linksQueue)[randomIndex]];
    }

    printLinksQueue() {
        console.log('linksQueue:', this.linksQueue);
    }
}

const linksRedirectsQueue = new LinksRedirectsQueue();
export default linksRedirectsQueue;
