class TrackNumbersQueue {

    constructor() {
        // tracksQueue's format: ['UK123456345HG','UK465632433HG',...];
        this.tracksQueue = [];
        this.maxLength = 10;
    }

    checkNotAlreadyTracked(trackNumber) {
        if (!this.checkTrackExistsInQueue(trackNumber)) {
            if (this.tracksQueue.length >= this.maxLength) {
                this.deleteLastElementInQueue();
            }
            this.addNewElementToQueue(trackNumber);
            return true;
        }
        return false;
    }

    checkTrackExistsInQueue(trackNumber) {
        return this.tracksQueue.includes(trackNumber);
    }

    addNewElementToQueue(trackNumber) {
        this.tracksQueue.push(trackNumber);
    }

    deleteLastElementInQueue() {
        this.tracksQueue.pop();
    }

    printTrackQueue() {
        console.log('tracksQueue:', this.tracksQueue);
    }
}

const trackNumbersQueue = new TrackNumbersQueue();
export default trackNumbersQueue;
