import {
  CLEAR_ACTIVE_TAB_ITEM_DATA, PRINT_REDUCER_EVENT, SET_PRELOADER_ACTIVE_TAB_ITEM_DATA,
  UPDATE_ACTIVE_TAB_ITEM_DATA
} from '../constant';

export default function activeTabItemDataReducer (state = {
  img: "",
  img_all: [],
  title: "",
  description: "",
  url: "",
  origin: "",
  price: "",
  currency: "",
  checkAffiliateStatus: -2,
  fullData: false,
  preloader: false,
}, action) {
  switch (action.type) {
    case UPDATE_ACTIVE_TAB_ITEM_DATA:
      return {
        ...state,
        ...action.product,
      };
    case CLEAR_ACTIVE_TAB_ITEM_DATA:
      return {
        img: "",
        img_all: [],
        title: "",
        description: "",
        url: "",
        origin: "",
        price: "",
        currency: "",
        checkAffiliateStatus: -2,
        fullData:false,
        preloader: false
      };
    case SET_PRELOADER_ACTIVE_TAB_ITEM_DATA:
      return {
        ...state,
        preloader: action.value
      };

    default:
      return {
        ...state
      };
  }
}
