import {CLEAR_SIMILAR_ITEMS, SET_SIMILAR_ITEMS, SET_SIMILAR_ITEMS_CHECK_AFF} from '../constant';

export default function similarItemsReducer(state = {
    items: [],
    itemCurrency: 'руб',
}, action) {
    switch (action.type) {
        case SET_SIMILAR_ITEMS:
            return {
                items: action.value,
                itemCurrency: action.itemCurrency,
            };

        case SET_SIMILAR_ITEMS_CHECK_AFF:
            return {
                items: state.items.map((item) => {
                    return {
                        ...item,
                        has_cashback: action.checkAffDataArr[item.link.split('?')[0]] !== undefined ? action.checkAffDataArr[item.link.split('?')[0]] : 0
                    }
                }),
                itemCurrency: state.itemCurrency,
            };
        case CLEAR_SIMILAR_ITEMS:
            return {
                items: [],
                itemCurrency: 'руб',
            };
        default:
            return {
                ...state
            };
    }
}
