import jQuery from 'jquery';
const $ = window.jQuery = jQuery;

class AliSellerData {

  constructor () {
    this.sellerData = {
      photo: [],
      sellerData:{}
    };
  }

  matchSellerTime (ownerMemberId, call = () => {
  }) {
    try {

      fetch('https://feedback.aliexpress.com/display/evaluationDetail.htm?ownerMemberId='.concat(ownerMemberId)).then(function (response) {
        return response.text();
      }).then( (text) => {

        var h = $(text),
          tableSellerSum = h.find('#feedback-summary table')[0] || h.find('#feedback-summary table');

        var match = $(tableSellerSum).find('tbody').html().match(/<([^<> ]*)>[\d, ]([^>]*)[\d, ]<\/([^<>]*)>/g)[0],
          dateText = match.replace(/<(?:.|\n)*?>/gm, '').trim();

        var storeDate = new Date(dateText),
          currentDate = new Date();

        var detailedTable = h.find('#feedback-dsr table')[0],
          trs = $(detailedTable).find('em:not([class])');
        if (trs.length == 0) {
          var trs0 = '0',
            trs1 = '0',
            trs2 = '0';
        }

        let negative = this.getValAnswer(h,'a[value="negative-6"]');
        let neutral = this.getValAnswer(h,'a[value="neutral-6"]');
        let positive = this.getValAnswer(h,'a[value="positive-6"]');

        // var negative = parseInt(h.find('a[value="negative-6"]')[0].innerText.replace(/,/g, ''), 10),
        //   neutral = parseInt(h.find('a[value="neutral-6"]')[0].innerText.replace(/,/g, ''), 10),
        //   positive = parseInt(h.find('a[value="positive-6"]')[0].innerText.replace(/,/g, ''), 10);

        call({
          storeTime: Number(
            (currentDate.getFullYear() * 12 + currentDate.getMonth()) - (storeDate.getFullYear() * 12 + storeDate.getMonth())
          ),
          described: parseFloat(trs0 || trs[0].innerText || 0),
          communication: parseFloat(trs1 || trs[1].innerText || 0),
          shippingSpeed: parseFloat(trs2 || trs[2].innerText || 0),
          lastPositiveFeedbacks: Number(positive) || 0,
          lastNegativeFeedbacks: Number(negative + neutral) || 0
        });
      });

    } catch (e) {
      return undefined;
    }
  }

  getValAnswer (h,selector) {
    return h.find(selector)[0] !== undefined ? parseInt(h.find(selector)[0].innerText.replace(/,/g, ''), 10) : 0;
  }

  matchRatingByFeedBack (a, callback, p) {
    var c = 'https://feedback.aliexpress.com/display/productEvaluation.htm',
      d = a;
    d.memberType = 'seller';
    d.evaStarFilterValue = p + ' Stars';
    d.evaSortValue = 'sortdefault@feedback';
    d.jumpToTop = 'false';
    d.withPersonalInfo = 'false';
    d.withAdditionalFeedback = 'false';
    d.withPictures = 'true';
    d.onlyFromMyCountry = 'false';
    d.i18n = 'true';
    d.startValidDate = '';

    $.post(c, d).done(function (html) {
      var h = $(html),
        res = {},
        ratingArr = [],
        imgsArr = [];

      var ratings = h.find('.rate-list').find('.fb-star-list-href');
      $.each(ratings, function (i, rEl) {
        ratingArr.push($(rEl).html());
      });

      $.each(h.find('img'), function (i, iEl) {
        var imgItem = $(iEl).attr('src');
        if (imgItem) {
          imgsArr.push(imgItem)
        }
      });

      res['stars'] = Number(h.find('#cb-withPictures-filter').closest('label').find('em').html()) || 0;
      res.images = imgsArr;

      callback(res);
    });
  }

  getSumPositiveWeight (x) {
    switch (true) {
      case (0 <= x && x < 6):
        return (4 * 1);
      case (6 < x && x <= 11):
        return (4 * 0.7);
      case (11 < x && x < 20):
        return (4 * 0.5);
      case (20 <= x):
        return (4 * 0);
    }
  }

  getSumNegativeWeight (x) {
    switch (true) {
      case (80 <= x):
        return (30 * 1);
      case (60 < x && x < 80):
        return (30 * 0.7);
      case (10 < x && x <= 60):
        return (30 * 0.5);
      case (10 >= x):
        return (30 * 0);
    }
  }

  getRateWeight (x) {
    switch (true) {
      case (x <= 3.8):
        return (17 * 1);
      case (3.8 < x && x <= 4.2):
        return (17 * 0.7);
      case (4.2 < x && x <= 4.5):
        return (17 * 0.5);
      case (4.5 < x):
        return (17 * 0);
    }
  }

  getNumOrdersWeight (x) {
    switch (true) {
      case (0 <= x && x < 100):
        return (15 * 1);
      case (100 <= x && x <= 500):
        return (15 * 0.7);
      case (500 < x && x <= 1000):
        return (15 * 0.5);
      case (1000 < x):
        return (15 * 0);
    }
  }

  getItemOrderPercentWeight (x) {
    switch (true) {
      case (0 <= x && x < 20):
        return (5 * 1);
      case (20 <= x && x < 40):
        return (5 * 0.7);
      case (40 <= x && x < 80):
        return (5 * 0.5);
      case (80 <= x && x <= 100):
        return (5 * 0);
    }
  }

  getDescWeight (x) {
    switch (true) {
      case (x < 4):
        return (34 * 1);
      case (4 <= x && x <= 4.3):
        return (34 * 0.7);
      case (4.3 < x && x <= 4.5):
        return (34 * 0.5);
      case (4.5 < x):
        return (34 * 0);
    }
  }

  getSellerTimeWeight (x) {
    switch (true) {
      case (x <= 6):
        return (32 * 1);
      case (6 < x && x <= 9):
        return (32 * 0.7);
      case (9 < x && x <= 12):
        return (32 * 0.5);
      case (12 < x):
        return (32 * 0);
    }
  }

  getPositFeedbacksWeight (x) {
    switch (true) {
      case (x <= 300):
        return (26 * 1);
      case (300 < x && x <= 500):
        return (26 * 0.7);
      case (500 < x && x <= 1100):
        return (26 * 0.5);
      case (1100 < x):
        return (26 * 0);
    }
  }

  getSpeedWeight (x) {
    switch (true) {
      case (x <= 3.8):
        return (24 * 1);
      case (3.8 < x && x <= 4):
        return (24 * 0.7);
      case (4 < x && x <= 4.5):
        return (24 * 0.5);
      case (4.5 < x):
        return (24 * 0);
    }
  }

  getCommunicationWeight (x) {
    switch (true) {
      case (x <= 3.8):
        return (22 * 1);
      case (3.8 < x && x <= 4):
        return (22 * 0.7);
      case (4 < x && x <= 4.5):
        return (22 * 0.5);
      case (4.5 < x):
        return (22 * 0);
    }
  }

  getNegatPercentWeight (x) {
    switch (true) {
      case (x > 10):
        return (21 * 1);
      case (5 < x && x <= 10):
        return (21 * 0.7);
      case (2 < x && x <= 5):
        return (21 * 0.5);
      case (x <= 2):
        return (21 * 0);
    }
  }

  getSellerTotalRate () {
    var desc = this.sellerData.sDetail.described || 0,
      sTime = this.sellerData.sDetail.storeTime,
      posit = this.sellerData.sDetail.lastPositiveFeedbacks,
      speed = this.sellerData.sDetail.shippingSpeed,
      comm = this.sellerData.sDetail.communication,
      negat = this.sellerData.sDetail.lastNegativeFeedbacks,
      negatPercent = negat / ((negat + posit) / 100);

    desc = this.getDescWeight(desc);
    sTime = this.getSellerTimeWeight(sTime);
    posit = this.getPositFeedbacksWeight(posit);
    speed = this.getSpeedWeight(speed);
    comm = this.getCommunicationWeight(comm);
    negatPercent = this.getNegatPercentWeight(negatPercent);

    return (100 - desc - sTime - posit - speed - comm - negatPercent) / 20 || 0;
  }

  setReliabilityVerdict (rate) {
    var rateColor = 'grey';

    switch (true) {
      case (rate > 0 && rate <= 3):
        rateColor = 'red';
        break;
      case (rate > 3 && rate < 4):
        rateColor = 'orange';
        break;
      case (rate >= 4 && rate <= 5):
        rateColor = 'green';
        break;
    }

    return rateColor;
  }

  solveAliRates () {

    var sumP = 0, sumN = 0;

    if (Array.isArray(this.sellerData.feedbackPositivePercent) && this.sellerData.feedbackPositivePercent.length>0) {
      sumP = this.sellerData.feedbackPositivePercent.reduce(function (a, b) {
        return (Number(a) || 0) + (Number(b) || 0) || 0;
      });
    }

    if (Array.isArray(this.sellerData.feedbackNegativePercent) && this.sellerData.feedbackNegativePercent.length>0) {
      sumN = this.sellerData.feedbackNegativePercent.reduce(function (a, b) {
        return (Number(a) || 0) + (Number(b) || 0) || 0;
      });
    }

    var rate = this.sellerData.rate,
      numOrders = this.sellerData.numOrders,
      itemOrderPercent = Number(this.sellerData.itemVoicesTotal) / (Number(this.sellerData.feedbacks) / 100) || 0;
    sumP = Number(sumP) / (Number(this.sellerData.feedbacks) / 100) || 0;
    sumN = Number(sumN) / (Number(this.sellerData.feedbacks) / 100) || 0;
    this.sellerData.feedbackPositivePercent = sumP;

    var resultI = (100 - this.getSumPositiveWeight(sumP) - this.getSumNegativeWeight(sumN) - this.getRateWeight(rate) - this.getNumOrdersWeight(numOrders) - this.getItemOrderPercentWeight(itemOrderPercent)) / 20 || 0;
    this.sellerData.totalItemRate = resultI;
    var resultS = this.getSellerTotalRate();
    this.sellerData.totalSellerRate = resultS;
    if (this.sellerData.totalSellerRate < 0) {
      this.sellerData.totalSellerRate = 0;
    }

    if ((this.sellerData.goodPhoto + this.sellerData.badPhoto.length||0) && this.sellerData.itemVoicesTotal == 0) {
      resultI = 0;
      this.sellerData.totalItemRate = resultI;
    }

    this.sellerData.itemRateColorContent = this.setReliabilityVerdict(resultI);

    if ((this.sellerData.sDetail.lastPositiveFeedbacks + this.sellerData.sDetail.lastNegativeFeedbacks) <= 100 && this.sellerData.sDetail.storeTime >= 24) {
      resultS = 0;
    }

    this.sellerData.sellerRateColorContent = this.setReliabilityVerdict(resultS);

    return true;
  }

  matchItemPhoto (ownerMemberId, productId, companyId) {
    var tmpObject = {
      feedbackNegativePercent: [],
      feedbackPositivePercent: [],
      badPhoto: [],
      goodPhoto: [],
    };
    ["1", "2", "3", "4", "5"].forEach((i) => {
      this.matchRatingByFeedBack({
        ownerMemberId: ownerMemberId,
        productId: productId,
        companyId: companyId
      }, function (param) {
        tmpObject['r_' + i] = param;

        if (i == 5 || i == 4) {
          tmpObject.feedbackPositivePercent.push(param.stars);
          Array.prototype.push.apply(tmpObject['goodPhoto'], param.images);
        }

        if (i <= 3) {
          tmpObject.feedbackNegativePercent.push(param.stars);
          Array.prototype.push.apply(tmpObject['badPhoto'], param.images);
        }
      }, i)

    });

    setTimeout(() => {
      Object.assign(this.sellerData, tmpObject);
      this.solveAliRates();
    }, 1000);
  }

  parseByPattern (value) {
    var pattern = 'window\.runParams\.' + value.trim() + '="(\\d{0,20})"',
      re = new RegExp(pattern);

    return $(document).find('body').html().match(re) ? $(document).find('body').html().match(re)[1] : null;
  }


  parseByPatternNew (value) {
    var pattern = '(?:"' + value.trim() + '":)(\\d{0,20})',
      re = new RegExp(pattern);

    return $(document).find('body').html().match(re) ? $(document).find('body').html().match(re)[1] : null;
  }

  async init (callback = () => {
  }) {
    // только для aliexpress
    if (window.location.href.indexOf('aliexpress.com') === -1 && window.location.href.indexOf('aliexpress.ru') === -1) {
      return false;
    }

    var tmpDataObj = {};

    tmpDataObj.ownerMemberId = $('.send-mail-btn[data-id1]').attr('data-id1')
      || $('.ui-atm[data-id1]').attr('data-id1')
      || this.parseByPattern('adminSeq')
      || this.parseByPatternNew('sellerAdminSeq');

    tmpDataObj.companyId = this.parseByPattern('companyId') || this.parseByPatternNew('companyId');
    tmpDataObj.productId = this.parseByPattern('productId') || this.parseByPatternNew('productId');

    this.parseMain(tmpDataObj.ownerMemberId,tmpDataObj.productId,tmpDataObj.companyId, callback)
  }

  parseSellerDetail(data, callback = () => {}){
    this.sellerData = data;
    this.matchSellerTime(this.sellerData.ownerMemberId, (data) => {
      this.sellerData.sDetail = data;
      callback(this.sellerData)
    });
  }

  parseMain (ownerMemberId,
             productId,
             companyId,
             callback = () => {
             }) {

    let tmpDataObj = {
      ownerMemberId : ownerMemberId,
      productId : productId,
      companyId : companyId
    };
    this.sellerData.sDetail = {
      storeTime: parseInt($('.store-open-time').find('span').html(), 10) * 12
    };

    this.matchItemPhoto(
      tmpDataObj.ownerMemberId,
      tmpDataObj.productId,
      tmpDataObj.companyId
    );

    tmpDataObj.numOrders = parseInt($('#j-order-num').html(), 10) || 0;
    if ($('.product-reviewer-sold').length > 0) {
      tmpDataObj.numOrders = parseInt($('.product-reviewer-sold').html(), 10) || 0;
    }

    var rateDom = $('span[class="percent-num"]')[0] || 0,
      aliTabs = $('#j-product-tabbed-pane');

    tmpDataObj.feedbacks = (aliTabs.find('li[data-trigger="feedback"] a')[0] ? parseInt(aliTabs.find('li[data-trigger="feedback"] a')[0].innerText.replace(/^\D+/g, '')) : 0);

    if ($('.overview-rating-average').length > 0) {
      if ($('.overview-rating-average[itemprop="ratingValue"]').length > 0) {
        rateDom = $('.overview-rating-average[itemprop="ratingValue"]')[0];
        tmpDataObj.feedbacks = parseInt($('.product-reviewer-reviews[itemprop="reviewCount"]')[0].innerText.replace(/^\D+/g, ''));
      }
      else {
        rateDom = $('.overview-rating-average')[0];
        tmpDataObj.feedbacks = parseInt($('.product-reviewer-reviews')[0].innerText.replace(/^\D+/g, ''));
      }

      tmpDataObj.itemVoices5 = Number(this.parseByPatternNew('fiveStarNum')) || 0;
      tmpDataObj.itemVoices4 = Number(this.parseByPatternNew('fourStarNum')) || 0;
    } else {
      if ($('span[data="5 Stars"]').length != 0) {
        tmpDataObj.itemVoices5 = Number($('span[data="5 Stars"]')[0].innerText.replace(/\,/g, ''));
      } else tmpDataObj.itemVoices5 = 0;

      if ($('span[data="4 Stars"]').length != 0) {
        tmpDataObj.itemVoices4 = Number($('span[data="4 Stars"]')[0].innerText.replace(/\,/g, ''));
      } else tmpDataObj.itemVoices4 = 0;
    }

    tmpDataObj.itemVoicesTotal = tmpDataObj.itemVoices5 + tmpDataObj.itemVoices4;
    tmpDataObj.rate = Number(rateDom.innerText) || 0;

    tmpDataObj.itemOrderPercent = Number(tmpDataObj.feedbacks) / (Number(tmpDataObj.numOrders) / 100) || 0;
    Object.assign(this.sellerData, tmpDataObj);

    setTimeout(() => {
      // chromeLib.sendMessage(SET_SELLER_DATA, this.sellerData)
      callback(this.sellerData)
    }, 2500);
  }
}

const aliSellerData = new AliSellerData();
export default aliSellerData;
