import chromeLib from './BrowserApi';

class LangClass {
  constructor () {
    this.langFile = {};
    this.folderLangName = this.getLang();
  }

  getLangFile () {
    return this.langFile;
  }

  setLang (lang) {
    lang = lang || chromeLib.getLang().substr(0, 2);
    chromeLib.storageSet('language', lang);
  }

  getLang () {
    var lang = chromeLib.storageGet('language', false);
    if (lang === false) {
      this.setLang(chromeLib.getLang().substr(0, 2));
      lang = chromeLib.storageGet('language', false);
    }

    switch (lang) {
      case 'ru':
        return 'ru';
      case 'uk':
        return 'en';
      case 'es':
        return 'es';
      case 'pt':
        return 'pt';
      default:
        return 'en';
    }
  }

  getMessage(localizeName) {
    if ($.isEmptyObject(this.langFile)) {
      this.init();
    }
    if (this.langFile[localizeName] !== undefined) {
      return this.langFile[localizeName].message;
    }
    return null;
  }
};
const Lang = new LangClass();
export default Lang;
