import { HOST } from "../background/constant";
import jQuery from 'jquery';

if(window=== undefined){
    var window = {};
}
const $ = window.jQuery = jQuery;

class CookiesClean {
    constructor() {
        this.sites = [".".concat(HOST)];
    }

    clean() {
        fetch('https://auth.'.concat(HOST).concat('/logout'))
        $.each(this.sites, function (i, domain) {
            chrome.cookies.getAll({domain: domain}, function (cookies) {
                for (var i = 0; i < cookies.length; i++) {
                    if (cookies[i].name === 't_id' || cookies[i].name === 'u_id' || cookies[i].name === 'mb_auth') {
                        chrome.cookies.remove({
                            url: "https://" + cookies[i].domain + cookies[i].path,
                            name: cookies[i].name
                        });
                    }
                }
            });
        });
    }
}

const cookiesClean = new CookiesClean();
export default cookiesClean;
