import {SET_OPTION_CHECKBOX, SET_OPTION_TAB} from '../constant';
import chromeLib from '../../lib/BrowserApi';

export default function optionsReducer(state = {
    shieldShowAli: chromeLib.storageGet("shieldShowAli", true),
    notifyEnabled: chromeLib.storageGet("notifyEnabled", true),
    cashback_notify: chromeLib.storageGet("cashback_notify", true),
    seller_reliability: chromeLib.storageGet("seller_reliability", true),
    amount_cashback_in_card: chromeLib.storageGet("amount_cashback_in_card", true),
    amount_cashback_in_search: chromeLib.storageGet("amount_cashback_in_search", true),
    add_btn_search_track: chromeLib.storageGet("add_btn_search_track", true),
    info_about_sites_with_cashback: chromeLib.storageGet("info_about_sites_with_cashback", true),
    google_and_yandex_show: chromeLib.storageGet("google_and_yandex_show", true),
    link_tooltips_show: chromeLib.storageGet("link_tooltips_show", true),
    show_similar_goods_on_hypermarkets_pages: chromeLib.storageGet("show_similar_goods_on_hypermarkets_pages", true),
    parcel_track_auto_send: chromeLib.storageGet("parcel_track_auto_send", true),
    tabName: 'main'
}, action) {
    switch (action.type) {
        case SET_OPTION_CHECKBOX:
            var obj = {};
            obj[action.name] = action.value;
            chromeLib.storageSet(action.name, action.value)

            return {
                ...state,
                ...obj
            }
        case SET_OPTION_TAB:
            return {
                ...state,
                ...{tabName: action.value}
            };
        default:
            return {
                ...state
            };
    }
}
