import chromeLib from './BrowserApi';
import helper from './Helper';
import {getUrlQueue} from '../background/reducers/urlQueue';
import {Offers} from '../background/reducers/offers';
import {
    OFFER_ACTIVE_SECONDS_LEAVE,
    REQUEST_CHECK_AFFILIATE,
    SET_OFFER_ACTIVE,
    SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED
} from "../background/constant";
import popupIconControl from "./PopupIconControl";
import autoRedirect from "./AutoRedirect";

window.getUrlQueue = getUrlQueue;
export default class CheckOfferActive {
    constructor(store) {
    }

    checkQueueForRedirect(store, tab) {
        // console.warn('checkQueueForRedirect');
        // получаем 2 последних Url из очереди редиректов
        let utlQueueTmp = getUrlQueue(store.getState().urlQueue, tab, 2);
        // console.log('checkQueueForRedirect 0000','getUrlQueue = ',utlQueueTmp)
        // вычисляем объекты для анализа по последним 2м Url
        let urlObjectBeforeLast = this.getOfferObjByUrl(utlQueueTmp[0]);
        let urlObjectLast = this.getOfferObjByUrl(utlQueueTmp[1]);

        console.log('checkQueueForRedirect 0', tab.url, urlObjectLast.offer)
        // если последний Url - наш оффер
        if (urlObjectLast.offer !== undefined) {

            if (urlObjectLast.offer.active !== "active") {
                // console.log('urlObjectLast.offer.active !== "active"');
                popupIconControl.setInactiveIcon();
                return;
            }
            // получаем кол-во редиректов для оффера
            // console.log('checkQueueForRedirect',"получаем кол-во редиректов для оффера" , store.getState().urlQueue)
            let utlQueue = getUrlQueue(store.getState().urlQueue, tab, urlObjectLast.countRedirect());
            // console.log('checkQueueForRedirect',utlQueue)

            // вычисляем объект для анализа по первому url (мог измениться, если кол-вo редиректов > 2)
            // console.log('checkQueueForRedirect',"вычисляем объект для анализа по первому url (мог измениться, если кол-вo редиректов > 2)")
            let urlObjectFirst = this.getOfferObjByUrl(utlQueue[0]);
            // console.log('urlObjectFirst',urlObjectFirst)
            // console.log('urlObjectLast.affLink',urlObjectLast.affLink())

            // если в первом Url найдена ссылка активации -> активируем кешбек
            console.log('checkQueueForRedirect 1',"если в первом Url найдена ссылка активации -> активируем кешбек", urlObjectFirst.url.indexOf(urlObjectLast.affLink()))
            if (urlObjectFirst.url.indexOf(urlObjectLast.affLink()) > -1 || this.findAffLinkInQueue(utlQueue, urlObjectLast.affLink())) {
                // активация
                console.log('checkQueueForRedirect 2 - активация', utlQueue);
                this.activate(store, tab, urlObjectLast.url);
            } else {
                let beforeLastDomain = urlObjectBeforeLast.urlInfo.domain !== "" ? urlObjectBeforeLast.urlInfo.domain : urlObjectBeforeLast.urlInfo.subdomain;
                let isBeforeLastUrlInWhiteList = store.getState().whiteList.list.indexOf(beforeLastDomain) > -1;
                console.log('checkQueueForRedirect 3', urlObjectLast.url, urlObjectBeforeLast.url , !isBeforeLastUrlInWhiteList)
                if (urlObjectBeforeLast.offer === undefined && !isBeforeLastUrlInWhiteList) {
                    // дизактивация
                    if (!autoRedirect.redirect(tab, urlObjectLast.offer)) {
                        console.log('checkQueueForRedirect 4','дизактивация')
                        this.deActivate(store, tab, urlObjectLast.url);
                    }

                } else if (!helper.isActiveCashback(urlObjectLast.offer)) {
                    console.log('checkQueueForRedirect 5',' 2 дизактивация',helper.isActiveCashback(urlObjectLast.offer),urlObjectLast.offer)

                    if (!autoRedirect.redirect(tab, urlObjectLast.offer)) {
                        console.log('checkQueueForRedirect 6','urlObjectLast.offer.active === "active"',urlObjectLast.offer.active)
                        if (urlObjectLast.offer.active === "active") {
                            this.deActivate(store, tab, urlObjectLast.url);
                        } else {
                            console.log('checkQueueForRedirect 7','urlObjectLast.offer.active === "active"', urlObjectLast.offer.active)
                            popupIconControl.setInactiveIcon();
                        }
                    }
                    // autoRedirect.redirect(tab, urlObjectLast.offer)
                } else if (autoRedirect.needAutoRedirectOnItemPage()) {
                    // активация
                    console.log('checkQueueForRedirect 8 - попытка активации ali auto_redirect on item page', utlQueue);
                    autoRedirect.redirect(tab, urlObjectLast.offer);
                }
            }
        }
    }

    // to check if activated from website and show that active
    findAffLinkInQueue(queue, affLink) {
        // console.log('findAffLinkInQueue', queue, affLink)
        for (let index in queue) {
            if (queue[index].indexOf(affLink) > -1) {
                if (queue[index - 1] && queue[index - 1].indexOf('megabonus.com/') > -1) {
                    return true;
                }
            }
        }
        return false;
    }

    /**
     * Активация кешбека
     * @param store
     * @param tab
     * @param url
     */
    activate(store, tab, url) {
        // console.warn('активация 1')
        // активация
        let activationTime = helper.getNowTimestamp() + OFFER_ACTIVE_SECONDS_LEAVE
        store.dispatch({
            type: SET_OFFER_ACTIVE,
            url: url,
            value: activationTime
        });
        popupIconControl.setActivatedIcon();

        autoRedirect.increaseCount(url);
        autoRedirect.checkNeedAutoActivateOnItemPage(url);
        this.requestCheckIcon(tab);

        // console.log('CheckOfferActivate - activate - SET_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED', store.getState(), helper.getNowTimestamp());
        // let offer = new Offers(store.getState().offers).getOffer(url);
        // to avoid duplication of activated popup appearing
        // if (offer && offer.activeCash && (activationTime - offer.activeCash) <= 2) {
            // console.warn('активация 2');
            store.dispatch({
                type: SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED,
                value: true,
                url: url
            });
        // }
    }

    /**
     * Деактивация кешбека
     * @param store
     * @param tab
     * @param url
     */
    deActivate(store, tab, url) {
        // console.warn('деактивация')
        // деактивация
        store.dispatch({
            type: SET_OFFER_ACTIVE,
            url: url,
            value: false
        });
        store.dispatch({
            type: SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED,
            value: false,
            url: url
        });
        popupIconControl.setDeactivatedIcon();
        this.requestCheckIcon(tab);
    }

    requestCheckIcon(store, tab, urlObjectLast) {
        setTimeout(() => {
            chromeLib.sendMessageToTab(tab, REQUEST_CHECK_AFFILIATE)
        }, 5000)
    }

    /**
     * Возвращает объект для упрощения работы с URL
     * @param url
     * @returns {{offer: null, init: (function(): init), offerDomain: null, affLink: (function(): string), active: active, urlInfo: null, countRedirect: (function(): number), objCurrentStatusCashback(): *, url: *}|*}
     */
    getOfferObjByUrl(url) {
        let offers = new Offers(store.getState().offers);
        let obj = {
            url: url,
            offerDomain: '',
            offer: null,
            urlInfo: null,
            init: function () {
                // console.log(offers)
                this.offer = offers.getOffer(this.url);
                if (this.offer !== undefined) this.offerDomain = this.offer.offer_domain;
                this.urlInfo = helper.getUrlInfo(this.url);
                return this;
            },
            countRedirect: function () {
                return Number((this.offer !== undefined && this.offer.count_redirect !== undefined) ? this.offer.count_redirect : 2);
            },
            affLink: function () {
                // https://ad.admitad.com/g/9vijc7ptzd493b3f64b67a660ebfae/?subid1=megab0nus.com&dl_target_url=none&subid=2365034_0.w4.n2p.v1i&ulp=https://best.aliexpress.com/
                // console.log('AFF_LINK',((this.offer !== undefined && this.offer.aff_link !== undefined) ? this.offer.aff_link : '').split('?')[0])
                if (this.offer !== undefined && this.offer.aff_link_clean !== undefined) {
                    return ((this.offer !== undefined && this.offer.aff_link_clean !== undefined) ? this.offer.aff_link_clean : '');
                }
                return ((this.offer !== undefined && this.offer.aff_link !== undefined) ? this.offer.aff_link : '').split('?')[0];
            },
            active: function () {
                if (this.offer !== undefined && this.offer.active !== undefined) return this.offer.active;
                return false;
            }
        };
        return obj.init();
    }
}


