import { PRINT_REDUCER_EVENT, SET_POPUP_PAGE, SET_PREVENT_PAGE } from '../constant';

export default function userReducer (state = {
  name: 'shop',
  prevent: 'user',
  preventQueue: ['user', 'user'],
  force: false
}, action) {
  switch (action.type) {
    case SET_POPUP_PAGE:
      state.preventQueue.push(action.value);

      if (state.preventQueue.length > 5) {
        state.preventQueue.shift();
      }

      return {
        name: action.value,
        force: action.force !== undefined ? !!action.force : false,
        prevent: state.preventQueue[state.preventQueue.length - 2],
        preventQueue: state.preventQueue
      };
    case SET_PREVENT_PAGE:

      if (state.preventQueue.length > 2) {
        state.preventQueue.pop();
      }

      return {
        ...state,
        name: state.preventQueue[state.preventQueue.length - 2],
        prevent: state.preventQueue[state.preventQueue.length - 1] ? state.preventQueue[state.preventQueue.length - 1] : state.preventQueue[state.preventQueue.length - 1],
        preventQueue: state.preventQueue
      };

    default:
      return {
        ...state
      };
  }
}
