import { SET_ACTIVE_OFFER_BY_API } from '../constant';

export default function activeOfferByApiReducer(state = {
    id: 0,
    happy_users: "",
    percent_conformity: 0,
    percent_delivery: 0,
    shop_average_rate: 0,
    percent_support: 0,
    link: "",
}, action) {
    switch (action.type) {

        case SET_ACTIVE_OFFER_BY_API:

            // prevent re-render
            var new_state = {
                ...state,
                ...action.value
            };

            if (JSON.stringify(new_state) === JSON.stringify(state)) {
                return state
            }

            return new_state;

        default:
            return {
                ...state
            };
    }
}
