import chromeLib from "./BrowserApi";

class PopupIconControl {
  constructor () {

    this.setDefaultConstants();
  }

  setDefaultConstants () {
    this.lastIconName = "";
    this.animationNow = false;
    this.stopAllAnimation = false;
    this.circleTimeout = null;
  }

  setIconAnimation (name = "grey", index , slideMax, timeoutAnimate, force = false, circle = false, steep = 0) {
    let imgPrefix = 'ic_';
    let imgPostfix = '.png';
    if (this.stopAllAnimation && index > 0 && !force) {
      clearTimeout(this.circleTimeout)
      return false;
    }

    if(name!==this.lastIconName && steep>0){
      clearTimeout(this.circleTimeout)
      return false;
    }

    let time = 0;
    if (force) {
      time = 1000;
      clearTimeout(this.circleTimeout)
      this.stopAllAnimation = true;
    }

    setTimeout(() => {
      this.stopAllAnimation = false;
      index = index < 1 ? 1 : index;
      this.lastIconName = name;

      slideMax = slideMax || 1;
      timeoutAnimate = timeoutAnimate || 100;

      if (index >= slideMax) {
        chromeLib.setIcon("/icons/popup/ic_icons/"
          .concat(imgPrefix)
          .concat(name)
          .concat('_')
          .concat(index)
          .concat(imgPostfix), true);

        if (circle) {
          this.circleTimeout = setTimeout(() => {
            this.setIconAnimation(name, 0, slideMax, timeoutAnimate, false, circle, ++steep);
            clearTimeout(this.circleTimeout)
          }, 5000)
        } else {
          this.animationNow = false;
        }

        return false;
      }

      this.animationNow = true;
      chromeLib.setIcon("/icons/popup/ic_icons/"
        .concat(imgPrefix)
        .concat(name)
        .concat('_')
        .concat(index)
        .concat(imgPostfix), true);

      if (!this.stopAllAnimation) {
        setTimeout(() => {
          this.setIconAnimation(name, index + 1, slideMax, timeoutAnimate,false, circle);
        }, timeoutAnimate);
      }
    }, time)
  }

  setActivatedIcon () {
    // console.warn('setActivatedIcon');
    this.setIconAnimation('green', 0, 1, 10, true)
  }

  setDeactivatedIcon (force = false) {
    // console.warn('this.setIco, setDeactivateIcon', this.lastIconName , force)
    // console.warn('this.setIco ===>', this.lastIconName )
    // console.warn('setDeactivatedIcon');

    if (this.lastIconName === 'attention' && force) {
      this.stopAllAnimation = true;
    }
    if (this.lastIconName !== 'attention' || force) {
      // console.warn('this.setIco, setDeactivateIcon START',1111)
      this.setIconAnimation('attention', 0, 24, 70, true, true)
    }
  }

  setInactiveIcon () {
    // console.warn('setInactiveIcon')
    this.setIconAnimation('grey', 0, 1, 70, true)
  }

  setIconOrange () {
    this.setIconAnimation('orange', 0, 1, 10, true)
  }

  setIconPreloader () {
    this.setIconAnimation('preloader', 0, 4, 10, true)
  }

}

const popupIconControl = new PopupIconControl();
export default popupIconControl;
