import {
    CLEAN_OFFERS,
    SET_OFFER_ACTIVE,
    SET_OFFER_PAUSED,
    SET_OFFERS,
    SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED
} from '../constant';

import helper from './../../lib/Helper'

export class Offers {
    constructor(state) {
        this.data = state;
    }

    getData() {
        return this.data;
    }

    isActive(url) {
        let offer = this.getOffer(url);
        return helper.isActiveCashback(offer)
    }

    isOurOffer(url) {
        if (url === undefined) {
            return false;
        }
        url = url.replace('aliexpress.ru', 'aliexpress.com');
        return this.getOffer(url) !== undefined;
    }

    /**
     * старая реализация для двухх офферов на одном домене и поддомене
     * реализовывалось для Tmall, сейчас это один оффер
     * @param url
     * @returns {Array}
     */
    getDomainMultiplicity(url) {
        try {
            url = new URL(url);
            var urlSplit = url.hostname.split('.'),
                domains = [];

            var urlLength = Number(urlSplit.length);
            for (var i = 0; i <= urlLength; i++) {
                for (var t = Number(i); t >= 0; t--) {
                    if (domains[t] === undefined) {
                        if (
                            urlSplit[i] !== undefined &&
                            (urlLength != t + 1)
                        ) {
                            domains[t] = urlSplit[i];
                        }
                    } else {
                        if (urlSplit[i] !== undefined) {
                            domains[t] += '.' + urlSplit[i];
                        }

                    }
                }
            }

            return domains;
        } catch (e) {
            return [];
        }
    }

    toPrettyPercent = (val, currency) => {
        return val.toFixed(2) + currency
    };

    getPercentage(activeTabOfferByApi, isAuth) {
        if (activeTabOfferByApi === undefined) return '';
        const {categories} = activeTabOfferByApi;

        let currency = '%';

        let min;
        let max = 0;
        let valueList = false;

        if (categories.length > 0 && typeof categories[0].category_currency === 'string') {
            currency = categories[0].category_currency;
        }

        let currencyList = categories.map((o) => o.category_currency.trim());
        let currencies = Array.from(new Set(currencyList));

        // если в категории есть валюта
        if (currencies.length > 1) {
            currency = '%';

            valueList = categories.filter((o) => {
                if (!o.category_percent > 0 || o.category_currency.trim() !== currency) {
                    return false; // skip
                }
                return true;
            }).map((o) => o.category_percent);

        } else {
            valueList = categories.filter((o) => {
                if (!o.category_percent > 0 || o.category_currency.trim() !== currency.trim()) {
                    return false; // skip
                }
                return true;
            }).map((o) => o.category_percent);
        }

        if (valueList.length === 0)
            return;

        min = Math.min.apply(Math, [...valueList.filter(Boolean)]);
        max = Math.max.apply(Math, [...valueList.filter(Boolean)]);

        if (max === min) {
            return this.toPrettyPercent(max, currency)
        }

        return this.toPrettyPercent(min, currency) + ' - ' + this.toPrettyPercent(max, currency)
    };

    getMaxPercentage(activeTabOfferByApi) {
        if (activeTabOfferByApi === undefined) return 0;
        const {categories} = activeTabOfferByApi;

        let currency = '%';

        let max = 0;
        let valueList = false;

        if (categories.length > 0 && typeof categories[0].category_currency === 'string') {
            currency = categories[0].category_currency;
        }

        let currencyList = categories.map((o) => o.category_currency.trim());
        let currencies = Array.from(new Set(currencyList));

        // если в категории есть валюта
        if (currencies.length > 1) {
            currency = '%';

            valueList = categories.filter((o) => {
                if (!o.category_percent > 0 || o.category_currency.trim() !== currency) {
                    return false; // skip
                }
                return true;
            }).map((o) => o.category_percent);

        } else {
            valueList = categories.filter((o) => {
                if (!o.category_percent > 0 || o.category_currency.trim() !== currency.trim()) {
                    return false; // skip
                }
                return true;
            }).map((o) => o.category_percent);
        }

        if (valueList.length === 0)
            return;

        max = Math.max.apply(Math, [...valueList.filter(Boolean)]);

        return this.toPrettyPercent(max, currency)
    };

    getOffers(url) {
        url = url.replace('aliexpress.ru', 'aliexpress.com');
        var offerDomain = helper.domainParts(url, 1);

        // console.log('offerDomain', offerDomain)
        if (this.data !== undefined && this.data[offerDomain] !== undefined) {
            return this.data[offerDomain];
        }
        return undefined;
    }

    getOfferDomain(url) {
        var offerData = this.getOffer(url);
        if (offerData !== undefined) return offerData.offer_domain;
        return '';
    }


    getOfferById(id) {
        for (var offerDomain in this.data) {
            for (var index in this.data[offerDomain]) {
                this.data[offerDomain][index].id

                if (Number(this.data[offerDomain][index].id) === Number(id)) return this.data[offerDomain][index]
            }
        }
        return undefined;
    }

    getOffer(url) {
        if (url === undefined) return undefined;

        url = url.replace('aliexpress.ru', 'aliexpress.com')

        if (url === ""
            || url.indexOf('chrome://') > -1
            || url.indexOf('s.click.aliexpress.com') > -1
            || url.indexOf('s.click.aliexpress.com') > -1
            || url.indexOf('thirdparty.aliexpress.com') > -1
            || url.indexOf('www.aliexpress.com/maintain.html') > -1
            || url.indexOf('login.aliexpress.com') > -1) return undefined;


        let offers = this.getOffers(url);
        // console.log('getDomainMultiplicity', this.getDomainMultiplicity(url))

        if (offers === undefined) {
            return offers;
        }
        var offerDomain = helper.domainParts(url, 1),
            offerThirdSubDomainDomain = helper.domainParts(url, 2),
            regular = '',
            regexpPathLength = 0,
            searchInfo = {
                offerDomain: offerDomain,
                searchIndex: -1,
                regexpPathLength: 0,
                key: 0,
            },
            searchIndex = -1;


        for (var offerKey in offers) {
            if (offers[offerKey].regexp !== undefined && offers[offerKey].regexp != null && offers[offerKey].regexp != "") {
                regular = new RegExp(offers[offerKey].regexp.replace('*', '\\w'), 'i');
                regexpPathLength = helper.getUrlInfo(offers[offerKey].regexp.replace('*', '')).pathname.length;
            } else {
                regular = new RegExp(offers[offerKey].offer_domain, 'i');
                regexpPathLength = 0;
            }

            searchIndex = url.search(regular);

            if (searchInfo.searchIndex == -1 || (searchInfo.searchIndex > searchIndex && searchIndex > -1) || (searchInfo.regexpPathLength < regexpPathLength && searchInfo.searchIndex == searchIndex && searchIndex > -1)) {
                searchInfo = {
                    offerDomain: offerDomain,
                    searchIndex: searchIndex,
                    regexpPathLength: regexpPathLength,
                    key: offerKey,
                };
            }
        }

        if (searchInfo.searchIndex > -1) return this.data[searchInfo.offerDomain][searchInfo.key];

        return undefined;
    }


    getUrlRedirectByUrl(url) {
        url = url || '';
        let offerData = this.getOffer(url)
        if (offerData !== undefined) {
            return helper.getUrlRedirect(offerData, url);
        }
        return url;
    }

    getUrlRedirectByUrlAndOffer(offerData, url = '') {
        if (offerData !== undefined) {
            var deep_link;
            let redirectUrl = offerData.aff_link;
            let isOrderPage = (url.indexOf('order_detail.htm') > -1 && (url.indexOf('trade.aliexpress.com') > -1 || url.indexOf('trade.aliexpress.ru') > -1 || url.indexOf('trade.alibaba.com') > -1));
            let isTmallPage = (url.indexOf('tmall.aliexpress.com') > -1);

            if (url.split('?')[0] === "") {
                deep_link = offerData.end_http;
            } else {
                deep_link = encodeURIComponent((isOrderPage || isTmallPage) ? url : url.split('?')[0]);
            }

            switch (offerData.provider) {
                case 'clickdealer':
                    redirectUrl = redirectUrl + '&s3=' + deep_link;
                    break;
                case 'cityads':
                    redirectUrl = redirectUrl + '&url=' + deep_link;
                    break;
                case 'rakuten':
                    redirectUrl = redirectUrl + '&murl=' + deep_link;
                    break;
                case 'awin':
                    redirectUrl = redirectUrl + '&p=' + deep_link;
                    break;
                case 'affise':
                    redirectUrl = redirectUrl + '&path=' + deep_link;
                    var offerObj = this.getOffer(url);
                    if (offerObj !== undefined && ([2952, 2949]).indexOf(Number(offerObj.id)) > -1)
                        redirectUrl = redirectUrl.replace('&path=', '&sub8=');
                    break;
                case 'admitad':
                    if (offerData.id === '463'
                        && (offerData.aff_link.indexOf('s.click.aliexpress.com') > -1 || offerData.aff_link.indexOf('s.click.aliexpress.ru') > -1)
                        && redirectUrl.indexOf('dl_target_url') === -1
                    ) {
                        redirectUrl = redirectUrl + '&dl_target_url=' + deep_link;
                    } else {
                        redirectUrl = redirectUrl + '&ulp=' + deep_link;
                    }
                    break;
                case 'gdeslon':
                    redirectUrl = redirectUrl + '&goto=' + deep_link;
                    break;
            }
            return redirectUrl;
        }
        return url;
    }
}

export default function offersReducer(state = {}, action) {
    var offers = null;
    switch (action.type) {
        case SET_OFFERS:
            var obj = {
                ...state
            };
            for (var variable in action.value) {
                // console.log('!!!',variable)

                if (obj[variable] === undefined) {
                    obj[variable] = [];
                }
                obj[variable] = [...obj[variable], ...action.value[variable]];
            }
            return obj;

        case CLEAN_OFFERS:
            return {};

        case SET_OFFER_ACTIVE:
            console.warn(SET_OFFER_ACTIVE)
            offers = new Offers(state);
            if (offers.getOffer(action.url) !== undefined) {
                // console.log("offers reducer :: SET_OFFER_ACTIVE activeCash and current timestamp and action.value", offers.getOffer(action.url).activeCash, Number((Date.now() / 1000).toFixed(0)), action.value);
                if (
                    action.value
                    && offers.getOffer(action.url).activeCash !== undefined
                    && offers.getOffer(action.url).activeCash > Number((Date.now() / 1000).toFixed(0))) {
                    return {
                        ...state
                    }
                }
                offers.getOffer(action.url).activeCash = action.value;
                // console.log("offers reducer :: SET_OFFER_ACTIVE - state CHANGED 1", offers.data['aliexpress.com'].activeCash);
            }
            return {
                ...state,
                ...offers.data
            };

        case SET_OFFER_PAUSED:
            offers = new Offers(state);
            if (offers.getOffer(action.url) !== undefined) {
                offers.getOffer(action.url).pausedTopline = action.value;
            }
            return {
                ...state,
                ...offers.data
            };

        case SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED:
            offers = new Offers(state);
            if (offers.getOffer(action.url) !== undefined) {
                if (
                    (offers.getOffer(action.url).showToplineActivated === undefined && action.value === false)
                    || (offers.getOffer(action.url).showToplineActivated === false && action.value === true)
                ) {
                    return {
                        ...state
                    };
                }
                offers.getOffer(action.url).showToplineActivated = action.value;
            }

            return {
                ...state,
                ...offers.data
            };

        default:
            return {
                ...state
            };
    }
}
