import { SET_ACTIVE_TAB_INFO, CLEAN_ACTIVE_TAB_INFO, PRINT_REDUCER_EVENT } from '../constant';

export default function activeOfferReducer (state = {
  url: "",
  id: 0,
}, action) {
  switch (action.type) {
    case SET_ACTIVE_TAB_INFO:
      return {
        ...state,
        ...action.value
      };

    case CLEAN_ACTIVE_TAB_INFO:
      return {
        url: "",
        id: 0,
      };
    default:
      return {
        ...state
      };
  }
}
