import { PRINT_REDUCER_EVENT, SET_LANG, SET_LANG_DATA } from '../constant';
import Lang from '../../lib/Lang';

export default function langReducer (state = {
  lang: Lang.getLang()
}, action) {
  switch (action.type) {
    case SET_LANG:
      if(action.value!== Lang.getLang()){
        Lang.setLang(action.value)
        return {
          ...state,
          lang: Lang.getLang()
        };
      }else{
        return {
          ...state
        };
      }
    case SET_LANG_DATA:
      let obj = {...state};
      for (var variable in action.value){
        obj[variable] = action.value[variable].message;
      }
      return obj;
    default:
      return {
        ...state
      };
  }
}
