import jQuery from 'jquery';

if (window === undefined) {
    var window = {};
}
const $ = window.jQuery = jQuery;

/**
 * библиотека работы с Chrome
 */
class BrowserApi {
    constructor() {
        /**
         * доп инфа по вкладкам
         */
        this.tabInfo = {};
        // количество окон
        this.countWindows = 0;

        this.lastIcon = "";
    }

    async getLangData(lang = 'ru') {
        let response = await fetch(chromeLib.getUrlMessagesFile(lang))
        return response.json();
    }

    setTabInfo(tabId, url, isProductPage = false, paymentPage = '') {
        this.tabInfo[tabId] = (this.tabInfo[tabId] !== undefined) ? this.tabInfo[tabId] : {};
        this.tabInfo[tabId].url = url;
        this.tabInfo[tabId].isProductPage = isProductPage;
        this.tabInfo[tabId].paymentPage = paymentPage;
        this.tabInfo[tabId].isPaymentPage = url === paymentPage;
    }

    deleteTabInfo(tabId) {
        if (this.tabInfo[tabId] !== undefined) delete this.tabInfo[tabId];
    }

    getTabInfo(tabId) {
        if (this.tabInfo[tabId] !== undefined) return this.tabInfo[tabId];
        return null;
    }

    getGlobalPage() {
        return chrome.extension.getBackgroundPage();
    }

    popupClose() {
        window.close();
    }

    getLang() {
        return chrome.i18n.getUILanguage();
    }

    getUrlMessagesFile(folderLangName) {
        return '_locales/' + folderLangName + '/messages.json';
    }

    getTabById(id, call) {
        chrome.tabs.get(id, call)
    }


    /**
     * Listeners
     */
    onChangedCookies(call) {
        chrome.cookies.onChanged.addListener(call);
    }

    onBeforeRequest(call) {
        chrome.webRequest.onBeforeRequest.addListener(call, {urls: ['<all_urls>']}, ['blocking']);
    }

    // onBeforeSendHeaders (call) {
    //   chrome.webRequest.onBeforeSendHeaders.addListener(call, {
    //     urls: ['<all_urls>'],
    //     types: ['main_frame']
    //   }, ['requestHeaders', 'blocking']);
    // }

    onCreateTab(call) {
    }

    onMessageExternal(call) {
        // В Firefox не поддерживается
        if (chrome.runtime.onMessageExternal) {
            chrome.runtime.onMessageExternal.addListener(call);
        }
    }

    onRemovedTab(call) {
        chrome.tabs.onRemoved.addListener(call)
    }

    onReplacedTab(call) {
        chrome.tabs.onReplaced.addListener(call);
    }

    onUpdatedTab(call) {
        chrome.tabs.onReplaced.addListener(call);
    }

    onActivatedTab(call) {
        chrome.tabs.onActivated.addListener(call);
    }

    setUninstallURL(call) {
        chrome.runtime.setUninstallURL(call);
    }

    storageGet(key, def) {
        var out = def;
        if (localStorage[key] !== undefined) {
            out = JSON.parse(localStorage[key]);
        }
        return out;
    }

    storageSet(key, value) {
        try {
            localStorage[key] = JSON.stringify(value);
        } catch (e) {
            // console.info('Ошибка записи в Storage (' + key + ')', "DATA", value, 'ERROR =>', e)
            this.storageCacheClear();
        }
    }

    storageCacheClear() {
        $.each(localStorage, function (key) {
            if (key.indexOf('cache_') > -1) {
                this.storageDel(key);
            }
        });
    }

    storageDel(key) {
        localStorage.removeItem(key)
    }

    /**
     * обработчик сообщений в Min
     */
    onMessage(call) {
        chrome.runtime.onMessage.addListener(function (request, sender, callback) {
            call(sender.tab, request);
        });
    }

    /**
     * обработчик сообщений Content
     */
    onMessageContent(call) {
        chrome.runtime.onMessage.addListener(function (message) {
            call(message);
        });
    }

    isChrome() {
        return (typeof safari === 'undefined' || safari === null);
    }

    setActivatedIcon() {
        this.setIcon('/icons/smalltile.png');
    }

    setIcon(path, force) {
        force = force || false;

        // console.warn('this.setIcon',path)
        if (this.lastIcon === path && force !== true) {
            return false;
        }
        // console.warn('this.lastIcon',this.lastIcon)
        this.lastIcon = path;
        chrome.browserAction.setIcon({
            path: path
        });
    }

    getPath(path) {
        return chrome.extension.getURL('/' + path);
    }

    reloadTab(tab) {
        chrome.tabs.update(tab.id, {url: tab.url});
    }

    removeTab(tab) {
        chrome.tabs.remove(tab.tabId !== undefined ? tab.tabId : tab.id);
    }

    redirectTab(tab, url) {
        chrome.tabs.update(tab.tabId !== undefined ? tab.tabId : tab.id, {url: url});
    }

    /**
     * отправка сообщения во вкладку
     */
    sendMessageToTab(tab, name, data = {}) {
        data.request = name;
        if (tab !== undefined && tab.id !== undefined) chrome.tabs.sendMessage(tab.id, data);
    }

    /**
     * открывает новую вкладку
     */
    openTab(url, call) {
        call = call || function () {
        };
        chrome.tabs.create({url: url, active: true}, call);
    }

    openWindow(url, call) {
        call = call || function () {
        };
        return chrome.windows.create({url: url}, function (windowI) {
            call(windowI.tabs[0])
        });
    }

    selectedWindow(windowId) {
        chrome.windows.update(windowId, {focused: true});
    }

    setActiveTab(tabId) {
        chrome.tabs.update(tabId, {active: true})
    }

    /**
     * переберет все вкладки во всех окнах
     */
    eachTabs(call) {
        this.eachWindow(function (window) {
            for (var i in window.tabs) {
                call(window.tabs[i])
            }
        })
    }

    allWindows(call) {
        chrome.windows.getAll({populate: true}, function (windows) {
            call(windows);
        });
    }

    /**
     * перебор всех окон
     */
    eachWindow(call) {
        this.allWindows(function (windows) {
            for (var i in windows) {
                call(windows[i]);
            }
        });
    }

    /**
     * вернет в call активную вкладку
     */
    getActiveTab(call) {
        var browser = browser || chrome;
        // if (this.detectBrowser() === 'Firefox') {
        // this.tabsInActiveWindow(function (tabs, windowId) {
        //     //     for (var i in tabs) {
        //     //       if (tabs[i].active) {
        //     //         call(tabs[i], windowId)
        //     //         // console.info(tabs[i].active,tabs[i])
        //     //       }
        //     //     }
        //     console.log('browserAPI'.tabs)
        // });
        // }else{
        browser.tabs.query({currentWindow: true, active: true}, (tabs, errors) => {
            // console.log('browserAPI',  tabs, errors);
            call(tabs[0]);
        });
        // }
        // chrome.tabs.query({"active": true, "currentWindow": true}, function (tabs) {
        //     call(tabs[0]);
        // })
    }

    /**
     * возвращает в call объект активной вкладки
     */
    tabsInActiveWindow(call) {
        this.getActiveWindow(function (window) {
            call(window.tabs, window.id);
        })
    }

    /**
     * возвращает в callback объект активного окна
     */
    getActiveWindow(call) {
        chrome.windows.getCurrent(function (windowFocused) {
            chrome.windows.getAll({populate: true}, function (windows) {
                // перебор окон
                for (var i in windows) {
                    if (windows[i].id === windowFocused.id) {
                        call(windows[i]);
                    }
                }
            });
        });
    }

    updatePopupIcon() {

    }

    getCookies(domain, call) {
        chrome.cookies.getAll({domain: domain}, call);
    }

    setCookies(name, data) {

    }

    getDomain() {
        return window.location.hostname;
    }

    getExtVersion() {
        return chrome.runtime.getManifest().version;
    }

    parseMessage(call) {

    }

    sendNotification(title, options) {
        if (this.detectBrowser() === 'Firefox') {
            // нет возвожности отправлять уведомления
            return false;
        }

        if (Notification === undefined) {
            // нет возвожности отправлять уведомления
            return false;
        }

        if (Notification.permission === "granted") {
            var notification = new Notification(title, options);

            notification.onclick =  () => {
                if (notification.tag.indexOf('payout') + 1) {
                    this.openTab(apiDomain + 'user/actions', '_blank');
                } else if (notification.tag.indexOf('ticket') + 1) {
                    this.openTab(apiDomain + 'user/tickets/show/' + notification.tag.substr(6), '_blank');
                } else if (notification.tag.substr(0, 11) == 'new_friend') {
                    this.openTab(apiDomain + 'user/referrals', '_blank');
                }
            }


            if (options.url !== undefined && options.url != "") {
                notification.onclick =  () => {
                    this.openTab(options.url);
                };
            }
        } else if (Notification.permission !== 'denied') {
            Notification.requestPermission(function (permission) {
                if (permission === "granted") {
                    var notification = new Notification(title, options);
                } else {
                }
            });
        } else {
        }
    }

    detectBrowser() {
        let ua = navigator.userAgent;
        let browserName = navigator.appName;
        if ((ua.indexOf("OPR/")) > -1)
            browserName = "Opera";
        else if (ua.indexOf('Edge/') > -1) {
            browserName = 'Edge';
        } else if ((ua.indexOf("Chrome")) > -1)
            browserName = "Chrome";
        else if ((ua.indexOf("Opera")) > -1)
            browserName = "Opera";
        else if ((ua.indexOf("MSIE")) > -1)
            browserName = "Microsoft Internet Explorer";
        else if ((ua.indexOf("Safari")) > -1)
            browserName = "Safari";
        else if ((ua.indexOf("Firefox")) > -1)
            browserName = "Firefox";
        else
            browserName = "Unknown";

        return browserName;
    }

    /**
     * отправка сообщения в background
     */
    sendMessage(name, data) {
        data = data || {};
        data.requestName = name;
        chrome.runtime.sendMessage(data);
    }
}

const chromeLib = new BrowserApi();
export default chromeLib;
