import helper from '../lib/Helper';
import cache from '../lib/Cache';
import chromeLib from '../lib/BrowserApi';
import Lang from './Lang';
import axios from 'axios';
import {
    CHECK_AFFILIATE_ON,
    HOST,
    NOTIFY_DATA_REQUEST_SECONDS_DELAY, NOTIFY_DATA_REQUEST_SECONDS_DELAY_STORAGE, OFFERS_DATA_REQUEST_SECONDS_DELAY,
    OFFERS_DATA_REQUEST_SECONDS_DELAY_STORAGE,
    OUT_USER
} from "../background/constant";

var config = {
    "domain": HOST,
    "protocol": "https",
    "port": 80,
    "development": false
}

class Api {
    constructor() {
        this.urlsSaved = {};
        this.saveItemNow = false;
        this.forbidApiRequest = 0;
        this.forbidApiRequestTimeout = 60 * 60 * 3;

        this.domain = config.domain;
        if (config.port !== 80) {
            this.domain = config.domain + ':' + config.port
        }
        this.api_url = config.protocol + '://' + this.domain + '/extensions3_2/';
        this.auth_api_url = config.protocol + '://auth.' + this.domain + '/api/';
    }

    /**
     * @param methodName
     * @param data
     * @param method
     * @param cacheEnabled
     * @returns {Promise<boolean|*>}
     */
    async requestApi(methodName, data = {}, method = 'post', cacheEnabled = false) {
        data.token = helper.storageGet('auth', '');
        data.project_name = 'cashback';
        data.device = 'extension';
        data.language = Lang.getLang();
        data.ext_version = chromeLib.getExtVersion();

        let formData = new FormData();
        for (var index in data) {
            formData.append(index, data[index]);
        }
        let storageKey = cache.getStorageMethodApiKey(methodName, formData);
        try {
            if (this.forbidApiRequest > +new Date()) {
                // Таймаут на запросы - режим высокой нагрузки
                throw "too_many_requests_timing";
            }


            let response = await axios[method]((['login', 'register'].indexOf(methodName) > -1 ? this.auth_api_url : this.api_url).concat(methodName), formData)
            if (cacheEnabled) {
                cache.set(storageKey, response.data)
            }

            /**
             * установка интервала по запросам по запросам
             */
            if (response.data.timing) {
                // Интервал запроса списка офферов
                if (response.data.timing.timeReloadOffersList) {
                    // input time in seconds !
                    chromeLib.storageSet('OFFERS_DATA_REQUEST_SECONDS_DELAY', response.data.timing.timeReloadOffersList);
                }
                // Интервал запроса браузерных уведомлений
                if (response.data.timing.timeReloadNotify) {
                    // input time in seconds !
                    chromeLib.storageSet(NOTIFY_DATA_REQUEST_SECONDS_DELAY, response.data.timing.timeReloadNotify);
                }
            }

            // Включение/отключение проверки на аффилярность
            if (response.data.needCheckAff !== undefined) {
                // input time in seconds !
                chromeLib.storageSet(CHECK_AFFILIATE_ON, Number(response.data.needCheckAff));
            }

            if (response.data.errorReason === "logout_ext") {
                this.logoutIsSuccessfully();
            }
            return response.data;

        } catch (error) {
            if (['login', 'register'].indexOf(methodName) > -1) {
                return error.response;
            }

            if (cacheEnabled) {
                // console.log('cache.get(storageKey)',cache.get(storageKey),storageKey)
                return cache.get(storageKey);
            }


            /**
             * Таймаут на запросы - режим высокой нагрузки
             */
            if (error === 'too_many_requests_timing') {
                return true;
            }
            if (error === 'Too Many Requests') {
                this.forbidApiRequest = +new Date() + this.forbidApiRequestTimeout;
            }

            if (methodName === 'error_ext_log_report') {
                return true;
            }
        }
    }

    /**
     * Сохранение данных о товаре
     * @param url
     * @param img
     * @param img_all
     * @param title
     * @param price
     * @param currency
     * @param description
     * @param category
     * @param brand
     * @param props
     * @returns {Promise<void>}
     */
    async saveProductData(url, img, img_all, title, price, currency, description, category, brand, props) {
        if (this.urlsSaved[url] === undefined) {
            if (this.saveItemNow) {
                setTimeout(() => {
                    this.saveProductData(url, img, img_all, title, price, currency, description, category, brand, props)
                    return true;
                }, 5000)
            }

            this.saveItemNow = true;
            this.urlsSaved[url] = 1;
            await this.requestApi("save_product_item_data_micro_type", {
                link: url,
                img: img,
                img_all: JSON.stringify(img_all),
                title: title,
                price: price,
                currency: currency,
                description: description,
                category: category,
                brand: brand,
                props: JSON.stringify(props),
            }).then(() => {
                this.saveItemNow = false;
                setTimeout(() => {
                    this.urlsSaved[url] = undefined;
                }, 10000)
            });


        }
    }

    /**
     * Сохранение данных о товаре
     * @param url
     * @returns {Promise<void>}
     */
    async checkAffiliateLink(url) {
        let data = await this.requestApi("check_affiliate_link", {
            page_url: url
        });

        helper.storageSet('check_affiliate', data.check_affiliate || false);
        if (data.errorReason) {
            return -1;
        } else {
            return data.data;
        }
    }

    /**
     * Сохранение данных о товарах
     * @param urls
     * @returns {Promise<void>}
     */
    async checkAffiliateByArray(urls) {
        let data = await this.requestApi("check_affiliate_arr", {
            urls: urls
        });
        helper.storageSet('check_affiliate', data.check_affiliate || false);
        return data;
    }

    /**
     * Добавление товара в Smart Cart
     * @param link
     * @param offer_name
     * @param offer_id
     * @returns {Promise<void>}
     */
    async addToSmartCart(link, offer_id, offer_name) {
        let {data} = this.requestApi('add_link_to_smart_cart', {
            link: link,
            offer_name: offer_name,
            offer_id: offer_id
        });
        return data;
    }

    /**
     * Получение статистики цены по товару
     * @param link
     * @param currency
     * @returns {Promise<void>}
     */
    async getPriceHistory(link, currency) {
        let {data} = await this.requestApi('get_price_history', {
            link: link,
            currency: currency,
        });
        return data;
    }

    /**
     * Удаление товара из Smart Cart
     * @param link
     * @returns {Promise<void>}
     */
    async removeFromSmartCart(link) {
        let {data} = this.requestApi('remove_link_from_sale_alert', {
            link: link,
        });
        return data;
    }

    /**
     * Получение данных по офферам
     * @returns {Promise<void>}
     */
    async getOffers(page) {
        return await this.requestApi('offers', {page: page, perPage: 300});
    }

    /**
     * Получение данных по магазинам с повышенным кэшбэком
     * @returns {Promise<void>}
     */
    async getIncreasedCashbackData() {
        return await this.requestApi('get_increased_shops');
    }

    /**
     * Получение данных по похожим товрам
     * @returns {Promise<void>}
     */
    async getSimilarProducts(brand, category) {
        return await this.requestApi('get_similar_products', {brand: brand, category: category});
    }

    /**
     * Получение PUSH уведомлений
     */
    async getNotification() {
        return await this.requestApi('get_notifications_list');
    }

    /**
     * Маркировка о разлогине
     * @returns {Promise<void>}
     */
    async logoutIsSuccessfully(page) {
        if (chromeLib.storageGet('need_logout', 0) > 0) {
            chromeLib.storageSet('need_logout', 10);
            await this.requestApi('logout_is_successfully');
        }
    }

    /**
     * Отмена заказа
     * @param orderId
     * @returns {Promise<boolean|*>}
     */
    async sendCancelOrderRequest(orderId) {
        return await this.requestApi('cancel_order', {order_id: orderId});
    }

    /**
     * Открытие спора по заказу
     * @param orderId
     * @returns {Promise<boolean|*>}
     */
    async sendOpenDebateOrderRequest(orderId) {
        return await this.requestApi('open_debate', {order_id: orderId});
    }

    /**
     * Подтверждение пользователем заказа на Aliexpress
     * @param orderId
     * @returns {Promise<boolean|*>}
     */
    async sendConfirmOrderRequest(orderId) {
        return await this.requestApi('confirm_shipping', {order_id: orderId});
    }

    /**
     * Получение данных по черному списку доменов,
     * на которых запрещено показывать карточки товаров в попапе
     * @returns {Promise<void>}
     */
    async getProductDisabledDomainList() {
        return await this.requestApi('get_product_disabled_domain_list');
    }

    /**
     * Создание кешбек ссылки
     * @param url
     * @returns {Promise<boolean|*>}
     */
    async createCashbackLink(url) {
        return await this.requestApi('create_short_link', {
            url: url
        });
    }

    async request(url, data = {}, method = 'post') {
        try {
            return await axios[method](url, data);
        } catch (error) {
        }
        return false;
    }

    /**
     * Получение данных по пользователю
     * @returns {Promise<void>}
     */
    async getUserData() {
        return await this.requestApi('user', {
            remember_me: true
        }, 'post');
    }

    /**
     * Получение всех магазинов, в которых пользователь делал покупки, включая отключенные.
     * @returns {Promise<void>}
     */
    async getMyOffers() {
        return await this.requestApi('get_my_offers', {
            remember_me: true
        }, 'post', true);
    }

    /**
     * Получение популярных магазинов
     * @returns {Promise<void>}
     */
    async getPopularOffers() {
        return await this.requestApi('get_popular_offers', {
            remember_me: true
        }, 'post', true);
    }

    /**
     * Получение товаров добавленных в Smart Cart
     * @returns {Promise<void>}
     */
    async getSmartCartList() {
        return await this.requestApi('get_smart_cart_list', {
            remember_me: true
        }, 'post');
    }

    /**
     * Получение данных по офферу
     * для зарегистрированного пользователя
     * @returns {Promise<void>}
     */
    async getOfferData(offerId) {
        return await this.requestApi('get_offer', {
            offer_id: offerId
        }, 'post');
    }

    /**
     * Сохранение информации об отслеживании в Parcel
     * @param trackName
     * @param trackNumber
     * @returns {Promise<void>}
     */
    async saveParcelTrack(trackName, trackNumber) {
        return await this.requestApi('save_parcel_track', {
            track_name: trackName,
            track_number: trackNumber,
        });
    }
}

const api = new Api();
export default api;
