var config = {
    'domain': HOST,
    'protocol': 'https',
    'port': 80,
    'development': false
};

window.helper = helper;
window.api = api;

class Background {
    constructor() {
        chromeLib.setUninstallURL(config.protocol + '://' + config.domain + '/uninstall?ext=true');
        //
        // // check cookie auth
        this.updateCookieToken();
        //
        // Listener megabonus cookie update and change auth user
        chromeLib.onChangedCookies(event => {
            this.onChangedCookies(event);
        });

        // listener message
        chromeLib.onMessage((tab, request, callback) => {
            setTimeout(() => {
                this.onMessage(tab, request, callback)
            }, 5)
        });

        chromeLib.onBeforeRequest((details) => {
            // console.warn('onBeforeRequest');
            setTimeout(() => {
                this.onBeforeRequest(details)
            }, 4)
        });

        chromeLib.onUpdatedTab(() => {
            this.updateBadge();
        });

        chromeLib.onActivatedTab((tab) => {
            chromeLib.getTabById(tab.tabId, (tab) => {
                if (chrome.runtime.lastError) { // sometimes chrome bug happens
                    // console.log(chrome.runtime.lastError.message);
                    return false;
                }
                if (tab.active) {
                    store.dispatch({
                        type: SET_POPUP_PAGE,
                        value: 'shop',
                        force: true
                    });
                    setTimeout(() => {
                        store.dispatch({
                            type: CLEAR_ACTIVE_TAB_ITEM_DATA,
                        });

                    }, 10)
                    setTimeout(() => {
                        if (tab.url.indexOf('//about:newtab') > -1) return;
                        store.dispatch({type: SET_ACTIVE_TAB_INFO, value: tab})
                        // запрос данных по товару
                        chromeLib.sendMessageToTab(tab, REQUEST_ACTIVE_TAB_ITEM_DATA);
                        // запрос данных по похожим товарам на алиекспресс
                        chromeLib.sendMessageToTab(tab, REQUEST_SIMILAR_ITEMS);
                        // обновление данных по активному офферу
                        this.updateActiveOffer(tab);

                        this.checkPopupIcon(tab);

                    }, 10)
                }

            })
        });

        this.urlQueue = {};

        chromeLib.onMessageExternal((request, sender, sendResponse) => this.onMessageExternal(request, sender, sendResponse));

        /**
         * Удаляем историю закрытой вкладки из памяти
         */
        chromeLib.onRemovedTab(function (tabId) {
            store.dispatch({
                type: DELETE_TAB_HISTORY,
                tabId: tabId
            })
        });

        this.init();

        setTimeout(() => {
            this.showRegisterTab();
        }, 1000)
    }

    init() {
        this.initLang();
        this.updateCookieToken();
        this.updateOffersData();
        this.getProductDisabledDomains();
        this.getUserPushNotification();
    }

    showRegisterTab() {
        if (chromeLib.storageGet('install_now', true)) {
            chromeLib.openTab(REGISTER_EXT_LINK);
            chromeLib.storageSet('install_now', false);
        }
    }

    updateBadge() {
        chromeLib.getActiveTab((tab) => {
            // if (chromeLib.isChrome()) {
            this.checkPopupIcon(tab);
            // }
        });
    }

    /**
     * проверка на аффилярность товара
     * @param tab
     * @returns {Promise<number>}
     */
    async getCheckAffiliateStatusByTab(tab) {
        let checkAffiliateStatus = 1;
        let updateIcon = true;
        if (tab.url.indexOf('aliexpress.') > -1 && helper.isEnabledCheckAffiliate()) {
            let storageKey = "cache_check_affiliate_".concat(tab.url.replace('aliexpress.ru', 'aliexpress.com').split('?')[0]);

            if (chromeLib.storageGet(storageKey, 'not') !== 'not') {
                checkAffiliateStatus = chromeLib.storageGet(storageKey, 'not');
            } else {
                popupIconControl.setIconPreloader();
                checkAffiliateStatus = await helper.cacheData('aff_link_'.concat(hash(tab.url.split('?')[0])), 10, async () => await api.checkAffiliateLink(tab.url))

                // пишем storage
                chromeLib.storageSet(storageKey, checkAffiliateStatus);

                // перетираем storage через 1 мин
                setTimeout(() => {
                    chromeLib.storageDel(storageKey);
                }, 60000);
            }

            if (store.getState().activeTabOffer.id > 0 &&
                !helper.isActiveCashback(store.getState().activeTabOffer)
            ) {
                checkAffiliateStatus = 1;
                updateIcon = false;
                popupIconControl.setDeactivatedIcon();
            }
        }
        if (checkAffiliateStatus === 0) {
            popupIconControl.setIconOrange();
        }

        if (checkAffiliateStatus > 0 && updateIcon) {
            this.checkPopupIcon(tab)
        }


        return checkAffiliateStatus;
    }

    /**
     * @param tab
     * @returns {boolean}
     */
    checkPopupIcon(tab) {
        if (tab !== undefined && tab.url !== undefined) {
            let offers = new Offers(store.getState().offers);
            let offerData = offers.getOffer(tab.url);

            let isOurOffer = offerData !== undefined;

            /**
             * Если не наш оффер
             * или оффер не активен
             */
            if (!isOurOffer || offerData !== undefined && offerData.active !== "active") {
                popupIconControl.setInactiveIcon();
                return true;
            }

            if (helper.isActiveCashback(offerData)) {
                popupIconControl.setActivatedIcon();
                return true;
            }

            /**
             * кешбек не активирован
             */
            if (offerData !== undefined && offerData.active === "active" && !helper.isActiveCashback(offerData)
            ) {
                popupIconControl.setDeactivatedIcon(true);
                return false;
            }

            if (tab.url.indexOf('aliexpress.') > -1) {
                if (chromeLib.storageGet("cache_check_affiliate_".concat(tab.url.replace('aliexpress.ru', 'aliexpress.com').split('?')[0]), 'not') !== 'not') {
                    if (chromeLib.storageGet("cache_check_affiliate_".concat(tab.url.replace('aliexpress.ru', 'aliexpress.com').split('?')[0]), 0) === 1) {
                        popupIconControl.setActivatedIcon()
                    } else {
                        popupIconControl.setIconOrange();
                    }
                } else {
                    popupIconControl.setActivatedIcon();
                }
            }
        } else {
            popupIconControl.setActivatedIcon();
        }
    }

    onBeforeRequest(details) {
        var tabId = details.tabId;
        if (details.type === 'main_frame' && details.frameId === 0 && details.parentFrameId === -1 && tabId > 0 && /^https?/i.test(details.url)) {

            chromeLib.getTabById(tabId, (tab) => {
                if (chrome.runtime.lastError) { // sometimes chrome bug happens
                    // console.log(chrome.runtime.lastError.message);
                    return false;
                }
                setTimeout(() => {
                    if (!(tab === undefined)) {
                        store.dispatch({
                            type: ADD_URL_TO_QUEUE,
                            tab: tab,
                            url: details.url
                        });

                        setTimeout(() => {
                            if (store.getState().urlQueue[tab.id] !== undefined && store.getState().urlQueue[tab.id][store.getState().urlQueue[tab.id].length - 1] !== tab.url.replace('aliexpress.ru', 'aliexpress.com')) {
                                checkOfferActive.checkQueueForRedirect(store, tab);
                            }
                        }, 10);
                    }
                }, 100)
            });
        }
    }

    onMessageExternal(request, sender, sendResponse) {
        if (request.disableByPriority) {
            sendResponse({disable: true});
        } else {
            return false;
        }
    }

    async getProductDisabledDomains() {
        let {data} = await api.getProductDisabledDomainList();

        store.dispatch({
            type: SET_PRODUCT_DISABLED_DOMAIN_LIST,
            value: data
        });
    }

    async updateOffersData() {
        chromeLib.storageSet(OFFERS_DATA_REQUEST_SECONDS_DELAY_STORAGE, helper.getNowTimestamp() + OFFERS_DATA_REQUEST_SECONDS_DELAY)
        store.dispatch({type: CLEAN_OFFERS})

        let maxPage = 40;
        let page = 1;
        while (page < maxPage && (offers_count === undefined || offers_count > 0)) {
            var {offers, white_list, offers_count} = await api.getOffers(page++);
            store.dispatch({
                type: SET_OFFERS,
                value: offers
            });
        }
        if (white_list) {
            white_list.push(HOST);
            store.dispatch({
                type: SET_WHITE_LIST,
                value: white_list
            });
        }

        // получение данных по повышенному кешбэку
        this.getIncreasedCashData();

        window.offers.data = store.getState().offers;

    }

    /**
     * получение данных по повышенному кешбэку
     * @returns {Promise<void>}
     */
    async getIncreasedCashData() {
        var {increased_cashback_shops, increased_cashback_popup_allowed_domains} = await api.getIncreasedCashbackData();

        if (increased_cashback_popup_allowed_domains) {
            store.dispatch({
                type: SET_INCREASED_CASHBACK_POPUP_ALLOWED_DOMAINS,
                value: increased_cashback_popup_allowed_domains
            });

            store.dispatch({
                type: SET_INCREASED_CASHBACK_SHOPS,
                value: increased_cashback_shops
            });
        }
    }

    /**
     * Получает данные языкового файла и отправляет REDUX
     * @param lang
     * @returns {Promise<void>}
     */
    async initLang(lang) {
        lang = lang || store.getState().lang.lang;

        store.dispatch({
            type: SET_LANG,
            value: lang
        });

        let LangData = await chromeLib.getLangData(lang);

        store.dispatch({
            type: SET_LANG_DATA,
            value: LangData,
            lang: lang
        });
    }

    /**
     * Обновление данных по пользователю
     * @returns {Promise<void>}
     */
    async updateUserData() {
        helper.storageSet(USER_DATA_REQUEST_DELAY, helper.getNowTimestamp() + USER_DATA_REQUEST_SECONDS_DELAY)

        let UserData = await api.getUserData();
        store.dispatch({
            type: SET_USER_DATA,
            value: UserData
        });
    }

    /**
     * Получение данных по пушам
     */
    async getUserPushNotification() {
        if (helper.isAuth(store) && store.getState().options.notifyEnabled) {
            chromeLib.storageSet(NOTIFY_DATA_REQUEST_SECONDS_DELAY_STORAGE, helper.getNowTimestamp() + chromeLib.storageGet(NOTIFY_DATA_REQUEST_SECONDS_DELAY, NOTIFY_DATA_REQUEST_SECONDS_DELAY_DEFAULT))
            let data = await api.getNotification();
            var index = 0;
            var value;
            if (data.push_notify.length > 0) {
                for (index in data.push_notify) {
                    try {
                        value = data.push_notify[index];
                        chromeLib.sendNotification(value.title, {
                            body: value.body,
                            icon: '../icons/notification.40x40.png',
                            tag: value.tag + index,
                            dir: 'auto',
                            url: (value.url !== undefined) ? value.url : ''
                        });
                    } catch (e) {
                        // console.warn(e)
                    }
                }
            }
        }
    }

    /**
     * Процедуры, которые необходимо выполнять раз в какое-то время
     * механизм завязан на Storage
     */
    checkIntervalProceduresStart() {
        let currentTimestamp = helper.getNowTimestamp();
        if (currentTimestamp >= helper.storageGet(OFFERS_DATA_REQUEST_SECONDS_DELAY_STORAGE, currentTimestamp)) {
            // console.log('Процедуры, которые необходимо выполнять раз в какое-то время OFFERS_UPDATE', helper.getNowTimestamp(),helper.storageGet(OFFERS_DATA_REQUEST_SECONDS_DELAY_STORAGE,-1))
            this.updateOffersData()
        }

        if (currentTimestamp >= helper.storageGet(USER_DATA_REQUEST_DELAY, currentTimestamp + 10)) {
            this.updateUserData();
        }

        let notifyDataRequestSecondsDelayStorage = helper.storageGet(NOTIFY_DATA_REQUEST_SECONDS_DELAY_STORAGE, currentTimestamp + 10);
        // MEG-3771 - issue where NOTIFY_DATA_REQUEST_SECONDS_DELAY_STORAGE was incorrect, so getUserPushNotification() never launched
        if (notifyDataRequestSecondsDelayStorage > currentTimestamp * 1.5) {
            notifyDataRequestSecondsDelayStorage = chromeLib.storageSet(NOTIFY_DATA_REQUEST_SECONDS_DELAY_STORAGE, currentTimestamp + NOTIFY_DATA_REQUEST_SECONDS_DELAY_DEFAULT);
        }
        if (currentTimestamp >= notifyDataRequestSecondsDelayStorage) {
            this.getUserPushNotification();
        }
    }

    /**
     * Background message listener worker
     * @param tab
     * @param request
     * @param callback
     */
    async onMessage(tab, request, callback = () => {
    }) {
        // var tabIndexFind = tab !== undefined ? this.tabsToClose.indexOf(tab.id) : -1;

        if (chromeLib.storageGet('need_logout', 0) > 0) {
            chromeLib.storageSet('need_logout', 0)
            store.dispatch({
                type: OUT_USER
            });
            CookiesClean.clean();
            window.open(AUTH_LINK)
            setTimeout(() => {
                store.dispatch({
                    type: SET_POPUP_PAGE,
                    value: 'shop',
                    force: true
                });
            }, 1000)

        }
        // console.log('mess', request.requestName)
        switch (request.requestName) {
            /**
             * Установка языка
             */
            case SET_LANG_DATA:
                await this.initLang(request.lang);
                break;
            case UPDATE_DATA:
                this.init();
                break;

            case RESET_INIT_PAGE:
                chromeLib.getActiveTab((tab) => {
                    store.dispatch({type: SET_ACTIVE_TAB_INFO, value: tab})
                    // запрос данных по товару
                    chromeLib.sendMessageToTab(tab, REQUEST_ACTIVE_TAB_ITEM_DATA);
                })
                break;

            case TAB_INIT:
                if (tab.active) {
                    this.checkIntervalProceduresStart();

                    setTimeout(() => {
                        checkOfferActive.checkQueueForRedirect(store, tab);
                    }, 10);
                    store.dispatch({type: SET_ACTIVE_TAB_INFO, value: tab})
                    // запрос данных по товару
                    chromeLib.sendMessageToTab(tab, REQUEST_ACTIVE_TAB_ITEM_DATA);

                    if (helper.isAliepress(tab.url)) {
                        // запрос данных по похожим товарам на алиекспресс
                        chromeLib.sendMessageToTab(tab, REQUEST_SIMILAR_ITEMS);
                    }
                    this.updateActiveOffer(tab);
                }

                break;

            case REQUEST_LINKS_DATA:
                let links = JSON.parse(request.links);
                let urlsArray = Object.values(links);
                let linksApproved = links;
                // console.log('linksApproved BEFORE: ', linksApproved);

                // very often there are a lof of url duplicates: to fetch just unique urls
                let urlsArrayFiltered = urlsArray.filter((value, index, self) => self.indexOf(value) === index);
                // limit just 30 first links to avoid high load of a client browser
                urlsArrayFiltered = urlsArrayFiltered.slice(0, 30);
                // console.log('urlsArrayFiltered: ', urlsArrayFiltered);
                let requests = urlsArrayFiltered.map(url => fetch(url));
                // todo before promise.all - check if result exists in linksRedirectsQueue.linksQueue
                Promise.all(requests).then(responses => {
                    let indexOfFilteredUrl = 0;
                    for (let key in linksApproved) {
                        indexOfFilteredUrl = urlsArrayFiltered.indexOf(linksApproved[key]);
                        if (indexOfFilteredUrl > -1) {
                            linksApproved[key] = responses[indexOfFilteredUrl]['url'];
                            // save to "link redirects" cache
                            linksRedirectsQueue.addLink(urlsArrayFiltered[key], linksApproved[key]);
                        }
                    }
                    // console.log('linksApproved AFTER: ', linksApproved);

                    if (Object.getOwnPropertyNames(linksApproved).length > 0) {
                        chromeLib.sendMessageToTab(tab, REQUEST_LINKS_DATA, linksApproved);
                    }
                });

                break;

            case REQUEST_LINK_DATA:
                let link = request.link;
                // console.log('REQUEST_LINK_DATA:', link);
                // check if the link redirect information is in "link redirects" cache
                let cachedLink = linksRedirectsQueue.getLinkValueByKey(link);
                if (!cachedLink) {
                    fetch(link).then(response => {
                        console.log('REQUEST_LINK_DATA response:', response);
                        linksRedirectsQueue.addLink(link, response['url']);
                        chromeLib.sendMessageToTab(tab, SEND_LINK_DATA, {
                            index: request.index,
                            originalUrl: link,
                            redirectedUrl: response['url']
                        });
                    });
                }
                else {
                    chromeLib.sendMessageToTab(tab, SEND_LINK_DATA, {
                        index: request.index,
                        originalUrl: link,
                        redirectedUrl: cachedLink
                    });
                }

                break;

            case GET_SEARCHERS_CASHBACK_INFO:
                var offers = new Offers(store.getState().offers),
                    offer = offers.getOffer(request.url),
                    isUrlOurOffer = offer !== undefined;

                // console.log('GET_SEARCHERS_CASHBACK_INFO',GET_SEARCHERS_CASHBACK_INFO, !isUrlOurOffer , offer, request.url)
                // не наш             // отключен                   // не разрешено в настройках
                if (!isUrlOurOffer || (offer !== undefined && (offer.active !== 'active' || Number(offer.show_in_search) === 0))) {
                    return false;
                }

                var dataForYandexGoogle = this.getYaGooData(request.url, offer),
                    res = {
                        'percent': dataForYandexGoogle.loyalty_percent,
                        'currency': dataForYandexGoogle.offer_currency,
                        'count_categories': dataForYandexGoogle.offer_categories_amount,
                        'li_eq': request.li_eq,
                        'provider': request.provider,
                        'country': dataForYandexGoogle.offer_country_flag,
                        'url': request.url
                    };

                chromeLib.sendMessageToTab(tab, 'INSERT_SEARCHERS_CASHBACK_DATA', res);
                break;

            case ALI_ORDER_ACTION_CANCEL_ORDER:
                api.sendCancelOrderRequest(request.orderId);
                break;
            case ALI_ORDER_ACTION_CONFIRM_SHIPPING:
                api.sendConfirmOrderRequest(request.orderId);
                break;
            case ALI_ORDER_ACTION_OPEN_DEBATE:
                api.sendOpenDebateOrderRequest(request.orderId);
                break;

            /**
             * Запрос на пересчет иконки по аффилярному товару
             */
            case REQUEST_CHECK_AFFILIATE:
                // проверка товара на аффилярность
                store.dispatch({
                    type: UPDATE_ACTIVE_TAB_ITEM_DATA,
                    product: {checkAffiliateStatus: await this.getCheckAffiliateStatusByTab(tab)}
                });
                break;

            /**
             * Активация из контента
             */
            case ACTIVE_TAB:
                if (helper.isAuth(store)) {
                    helper.clickActivateCashback(store.getState().activeTabOffer, request.componentName, tab);
                } else {
                    chromeLib.openTab(REGISTER_EXT_LINK);
                }
                break;

            case ADD_URL_TO_QUEUE:
                store.dispatch({
                    type: ADD_URL_TO_QUEUE,
                    tab: tab,
                    url: tab.url
                });
                break;

            /**
             * запрос на сохранение данных о товаре а API
             */
            case SAVE_ITEM_DATA:
                setTimeout(() => {
                    if (tab.active) {
                        let props = {
                            reviews: request.product.reviews,
                            orders_count: request.product.orders_count,
                            rating: request.product.rating,
                            characteristics: request.product.characteristics,
                            seller_data: request.product.seller_data,
                        }
                        api.saveProductData(request.product.url,
                            request.product.img,
                            request.product.img_all,
                            request.product.title,
                            request.product.price,
                            request.product.currency,
                            request.product.description,
                            request.product.category,
                            request.product.brand,
                            props
                        );
                    }
                }, 500);
                break;

            /**
             * запрос к активной вкладке на обновление данных о товаре в активной вкладке
             */
            case REQUEST_ACTIVE_TAB_ITEM_DATA:
                setTimeout(() => {
                    chromeLib.getActiveTab(async (tab) => {
                        chromeLib.sendMessageToTab(tab, 'REQUEST_ACTIVE_TAB_ITEM_DATA');
                        // проверка товара на аффилярность
                        store.dispatch({
                            type: UPDATE_ACTIVE_TAB_ITEM_DATA,
                            product: {checkAffiliateStatus: await this.getCheckAffiliateStatusByTab(tab)}
                        });
                    });
                }, 10)
                break;

            /**
             * отправка ответа на проверку аффилярности во вкладку запросившую данную првоверку
             */
            case CHECK_AFFILIATE:
                if (tab.active) {
                    store.dispatch({
                        type: UPDATE_ACTIVE_TAB_ITEM_DATA,
                        product: {checkAffiliateStatus: await this.getCheckAffiliateStatusByTab(tab)}
                    });
                }
                break;

            /**
             * создание кешбек ссылки и сохранение ее в Redux
             */
            case CREATE_CASHBACK_LINK:
                let {url_new} = await api.createCashbackLink(request.url.replace('aliexpress.ru', 'aliexpress.com'));
                store.dispatch({
                    type: CREATE_CASHBACK_LINK,
                    url: request.url,
                    link: url_new
                });
                break;

            /**
             * запрос из Content на получение данных по похожим товарам в апи
             */
            case GET_SIMILAR_ITEMS:

                if (tab.active) {
                    // очистка от старых данных перед запросом новых
                    store.dispatch({
                        type: CLEAR_SIMILAR_ITEMS,
                    });

                    var xmlHttp = new XMLHttpRequest();
                    xmlHttp.open('GET', request.query, false);
                    xmlHttp.send(null);
                    let data = (JSON.parse(xmlHttp.responseText));

                    if (Array.isArray(data.results) && data.results.length) {
                        if (data.results.some((item) => helper.parseFloats(item.maxPrice) < request.itemPrice)) {
                            store.dispatch({
                                type: SET_SIMILAR_ITEMS,
                                value: data.results.map((item, i) => {
                                    return {
                                        link: helper.cleanLink(item.productDetailUrl),
                                        img: helper.cleanLink(item.productImage),
                                        title: item.productTitle,
                                        price: item.maxPrice,
                                        has_cashback: -1
                                    };
                                }),
                                itemCurrency: request.itemCurrency,
                            });

                            let urls = [];
                            data.results.forEach((item, i) => {
                                urls.push(helper.cleanLink(item.productDetailUrl));
                            });

                            if (helper.isEnabledCheckAffiliate()) {
                                setTimeout(async () => {
                                    store.dispatch({
                                        type: SET_SIMILAR_ITEMS_CHECK_AFF,
                                        checkAffDataArr: await helper.cacheData('aff_arr_'.concat(hash(tab.url.split('?')[0])), 10, async () => (await api.checkAffiliateByArray(urls)).data)
                                    });
                                }, 20)
                            }

                        }
                    }
                    // console.log(GET_SIMILAR_ITEMS, 'OK')
                } else {
                    // console.log(GET_SIMILAR_ITEMS, 'ОТКЛОНЕН')
                }
                break;

            /**
             * Запрос данных по пользователю из API с сохранением результата в Redux
             */
            case UPDATE_USER_DATA:
                await this.updateUserData();
                break;

            // /**
            //  * Запрос данных по smart cart из API с сохранением результата в Redux
            //  */
            // case REQUEST_SA_ITEMS:
            //   store.dispatch({
            //     type: SET_SA_ITEMS,
            //     value: (await api.getSmartCartList()).data
            //   });
            //   break;

            /**
             * Запрос данных для блока проверки продавца
             */
            case REQUEST_SELLER_DATA:
                chromeLib.getActiveTab((tab) => {
                    chromeLib.sendMessageToTab(tab, REQUEST_SELLER_DATA)
                });
                break;
            /**
             * Запрос данных для блока проверки продавца
             */
            case SET_SELLER_DATA:

                store.dispatch({
                    type: SET_SELLER_DATA,
                    value: request
                });

                setTimeout(() => {
                    // console.log('FFF',store.getState().aliSellerData.sellerData.sellerData, request.sellerData)
                    // aliSellerData.sellerData.s
                    // aliSellerData.parseSellerDetail(store.getState().aliSellerData.sellerData.sellerData, (data) => {
                    aliSellerData.parseSellerDetail(request, (data) => {
                        aliSellerData.solveAliRates();

                        store.dispatch({
                            type: SET_SELLER_DATA,
                            value: {...data, totalSellerRate: aliSellerData.sellerData.totalSellerRate}
                        });
                    });
                }, 10)
                break;

            case ADD_SA_ITEMS:
                await api.addToSmartCart(request.link.replace('aliexpress.ru', 'aliexpress.com'), store.getState().activeTabOffer.id, store.getState().activeTabOffer.offer_name);
                let linkObj = {};
                linkObj[request.link.replace('aliexpress.ru', 'aliexpress.com')] = 0;
                store.dispatch({
                    type: ADD_SA_ITEMS,
                    linkObj: linkObj
                });
                //
                // setTimeout(async ()=>{
                //   store.dispatch({
                //     type: SET_SA_ITEMS,
                //     value: (await api.getSmartCartList()).data
                //   });
                // },10000)
                break;
            case GET_PRICE_HISTORY:
                setTimeout(async () => {
                    store.dispatch({
                        type: SET_PRICE_HISTORY,
                        value: await api.getPriceHistory(request.link, store.getState().activeTabItemData.currency)
                    });
                }, 200)

                break;
            case OUT_USER:
                CookiesClean.clean();
                break;

            case REMOVE_SA_ITEM:
                await api.removeFromSmartCart(request.link.replace('aliexpress.ru', 'aliexpress.com'));
                store.dispatch({
                    type: REMOVE_SA_ITEM,
                    link: request.link.replace('aliexpress.ru', 'aliexpress.com')
                });
                break;

            case GOOGLE_TAG_EVENT:
                chromeLib.getActiveTab((tab) => {
                    // console.log(GOOGLE_TAG_EVENT,['_trackEvent', request.category.toLowerCase(), request.action.toLowerCase(), this.getGoogleTagEventLabel(request.url||tab.url)],'URL',request.url||tab.url)
                    _gaq.push(['_trackEvent', request.category.toLowerCase(), request.action.toLowerCase(), this.getGoogleTagEventLabel(request.url || tab.url)]);
                })
                break;

            case GOOGLE_EVENT_SEND:
                    // console.log(GOOGLE_EVENT_SEND ,['_trackEvent', request.category.toLowerCase(), request.action.toLowerCase(), request.eventLabel.toLowerCase()]);
                    _gaq.push(['_trackEvent', request.category.toLowerCase(), request.action.toLowerCase(), request.eventLabel.toLowerCase()]);
                break;

            case UPDATE_ACTIVE_OFFER:
                chromeLib.getActiveTab((tab) => {
                    // console.log(UPDATE_ACTIVE_OFFER, tab)
                    this.updateActiveOffer(tab);
                });
                break;

            case GET_NO_OFFERS_PAGE_DATA:
                var myOffers = await api.getMyOffers();
                // console.log('myOffers', myOffers)

                if (myOffers !== undefined) {
                    store.dispatch({
                        type: SET_MY_OFFERS,
                        value: myOffers.data
                    });
                }

                var popularOffers = await api.getPopularOffers();
                var offers = new Offers(store.getState().offers);
                store.dispatch({
                    type: SET_POPULAR_OFFERS,
                    value: popularOffers.data
                });
                break;

            case ACTIVATE_CASHBACK_IN_ACTIVE_TAB:
                chromeLib.getActiveTab((tab) => {
                    // console.log(tab);
                    // var offers = new Offers(store.getState().offers);

                    helper.clickActivateCashback(store.getState().activeTabOffer, request.componentName, tab);

                    // chromeLib.redirectTab(tab, offers.getUrlRedirectByUrl(tab.url));
                });
                // api.userActivateClickLog(store.getState().activeTabOffer.id, request.componentName);
                break;

            case LOGIN_USER:
                var response = await api.requestApi('login', {
                    email: request.email.toString(),
                    password: request.password,
                    redirect_url: '',
                    project_name: 'cashback',
                    device: 'extension'
                });

                if (response.code !== 200) {
                    store.dispatch({
                        type: SET_ERROR_AUTH,
                        value: store.getState().lang['span_error']
                    })
                } else {

                    store.dispatch({
                        type: SET_USER_DATA,
                        value: response.user
                    });

                    store.dispatch({
                        type: SET_USER_TOKEN,
                        value: response.token
                    });
                }

                break;
            case GET_INCREASED_CASHBACK_DATA:
                this.getIncreasedCashData();
                break;

            /**
             * получение данных по повышенному кешбэку
             * @returns {Promise<void>}
             */
            case GET_SIMILAR_PRODUCTS:
                let {products, amount} = await api.getSimilarProducts(request.brand, request.category);
                chromeLib.sendMessageToTab(tab, 'SET_SIMILAR_PRODUCTS', {products: products});
                break;

            /**
             * запрос на сохранение информации о отслеживании посылки со страниц алиэкспресса
             */
            case SAVE_PARCEL_TRACK:
                // console.log('SAVE_PARCEL_TRACK before send: ', request);
                if (!request.trackNumber) {
                    break;
                }
                if (trackNumbersQueue.checkNotAlreadyTracked(request.trackNumber)) {
                    // console.log('SAVE_PARCEL_TRACK sent!');
                    setTimeout(() => {
                        if (tab.active) {
                            api.saveParcelTrack(
                                request.trackName ?? '',
                                request.trackNumber,
                            );
                        }
                    }, 500);
                }
                trackNumbersQueue.printTrackQueue();
                break;

            // case GET_ONBOARDING_SHOW:
            //   store.dispatch({
            //     type: GET_ONBOARDING_SHOW,
            //     value: request.name
            //   });
            //   break;
            default:
                // console.info('not known message name !!!', request.requestName)

                break;
        }
    }

    updateActiveOffer(tab) {
        var offers = new Offers(store.getState().offers);
        let offer = offers.getOffer(tab.url)
        // console.log('updateActiveOffer', offer)
        if (offer !== undefined) {
            store.dispatch({
                type: SET_ACTIVE_OFFER,
                value: offer
            });
            this.updateOfferDataByActiveTab(offer.id, tab)
        } else {
            store.dispatch({
                type: SET_ACTIVE_OFFER,
                value: undefined
            });
            this.updateOfferDataByActiveTab(0, tab)
        }
    }

    getGoogleTagEventLabel(url) {
        let storageKey = "cache_check_affiliate_".concat(url.replace('aliexpress.ru', 'aliexpress.com').split('?')[0]);
        return `${(helper.isActiveCashback(store.getState().activeTabOffer) ? "activated" : "not activated")}|${(chromeLib.storageGet(storageKey, 1) > 0 ? "affiliate" : "not affiliate")}|${(true ? "shop page" : "not shop page")}|${url.split("?")[0]}`
    }

    getYaGooData(url, offer) {
        var category_list_obj,
            res = [
                offer_categories_amount => 0,
                offer_currency => '%',
                offer_country_flag => '1',
                loyalty_percent => 0
            ];

        if (offer !== undefined) {
            category_list_obj = JSON.parse(offer.percentage_of_category_list_json);
            res.offer_categories_amount = Object.keys(category_list_obj).length;
            res.offer_currency = offer.offer_percent_currency;
            res.offer_country_flag = offer.country;
            res.loyalty_percent = offer.user_percent;
        }
        return res;
    }

    async updateOfferDataByActiveTab(offerID, tab) {
        if (offerID !== store.getState().activeTabOfferByApi.id) {
            if (offerID > 0) {
                var offerData = await api.getOfferData(offerID);
                store.dispatch({
                    type: SET_ACTIVE_OFFER_BY_API,
                    value: offerData.offer !== undefined ? {...offerData.offer, ...offerData.offer_reviews} : {id: 0}
                });

            } else {
                store.dispatch({
                    type: SET_ACTIVE_OFFER_BY_API,
                    value: {id: 0}
                });
            }
        }
        this.checkPopupIcon(tab);
    }

    onChangedCookies(details) {
        if ((details.cookie.domain.indexOf(config.domain)) > -1 && details.cookie.name === 't_id') {
            this.updateCookieToken();
        }
    }

    updateCookieToken() {
        chromeLib.getCookies(config.domain, (cookies) => {
            var u_id = 0, e_id = 0, wid = 0, token = 0, lastSocialAuth = "";
            cookies.forEach(function (cookie) {
                if (cookie.name === 'u_id') {
                    u_id = decodeURIComponent(cookie.value);
                }
                if (cookie.name === 'e_id') {
                    e_id = decodeURIComponent(cookie.value);
                }
                if (cookie.name === 'lastAuth' && cookie.value.length !== 0) {
                    try {

                        var lastTime = 0;
                        var lastSocial = '';
                        var tmpTime = 0;
                        var lastSocialAuthData = JSON.parse(decodeURIComponent(cookie.value));
                        for (var email in lastSocialAuthData) {
                            for (var emailDataKey in lastSocialAuthData[email]) {
                                switch (emailDataKey) {
                                    case "vk":
                                    case "fb":
                                    case "ok":
                                    case "google":
                                        if (lastSocialAuthData[email][emailDataKey].date !== undefined) {
                                            tmpTime = (new Date(lastSocialAuthData[email][emailDataKey].date.replace("+", ' '))).getTime() / 1000;

                                        }
                                        lastSocial = emailDataKey;
                                        break;
                                    default:
                                        break;
                                }

                                if (tmpTime > lastTime && emailDataKey !== "") {
                                    lastSocialAuth = emailDataKey;
                                    lastTime = tmpTime;
                                }
                                emailDataKey = "";
                                tmpTime = 0;
                            }
                        }
                    } catch (e) {
                        // console.warn(e)
                    }
                }
                if (cookie.name === 'webm_id') {
                    wid = decodeURIComponent(cookie.value);
                }
                if (cookie.name === 't_id') {
                    if (cookie.name === 't_id' && decodeURIComponent(cookie.value).length > 0) {
                        store.dispatch({
                            type: SET_USER_TOKEN,
                            value: decodeURIComponent(cookie.value)
                        })
                    }
                }
            });
            let lastUserId = Number(store.getState().user.userData.id)
            store.dispatch({
                type: SET_USER_DATA,
                value: {
                    id: u_id,
                    email: e_id,
                    wid: wid,
                    lastSocialAuth: lastSocialAuth
                }
            });
            // console.warn('cookies', 1)
            setTimeout(() => {
                this.updateUserData();

                // console.warn('cookies', 2)
            }, 100);
            setTimeout(() => {
                if (Number(store.getState().user.userData.id) !== lastUserId) {
                    this.updateOffersData();
                }
            }, 5000);
        });
    }
}


