import {SET_USER_DATA, SET_USER_TOKEN, OUT_USER, SET_ERROR_AUTH, PRINT_REDUCER_EVENT, LOGIN_USER} from '../constant';
import chromeLib from "../../lib/BrowserApi";

window._ = require('lodash');

export default function userReducer(state = {
    userData: {
        id: 0,
        email: "",
        photo_url: "icons/popup/user-avatar.svg",
        level: 1,
        user_name: '',
        lastSocialAuth: "",
    },
    errorAuth: "",
    token: chromeLib.storageGet('auth', '')!==""?chromeLib.storageGet('auth', ''):chromeLib.storageGet('info',{token:""}).token || "",
}, action) {
    if (state.token && state.token !== "") {
        chromeLib.storageSet('auth', state.token)
    }

    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                userData: {
                    ...state.userData,
                    ...action.value,
                },
                errorAuth: ""
            };

        case SET_USER_TOKEN:
            if (_.isEqual(action.value, state.token)) {
                return {
                    ...state
                };
            }

            chromeLib.storageSet('auth', action.value);
            return {
                ...state,
                token: action.value,
                errorAuth: ""
            };

        case SET_ERROR_AUTH:
            chromeLib.storageSet('auth', '');
            return {
                ...state,
                errorAuth: action.value
            };

        case OUT_USER:
            chromeLib.storageSet('auth', '');
            return {
                userData: {
                    id: 0,
                    email: "",
                    photo_url: "icons/popup/user-avatar.svg",
                    level: 1,
                    user_name: "",
                    lastSocialAuth: "",
                },
                errorAuth: "",
                token: ""
            };

        default:
            return {
                ...state
            };
    }
}
