import React from "react";
import chromeLib from "./BrowserApi";
import {CHECK_AFFILIATE_ON, HOST, OFFER_ACTIVE_SECONDS_LEAVE, SET_OFFER_ACTIVE} from "../background/constant";
import api from "./Api";

const lscache = require('lscache');
const UrlParse = require('url-parse');

class Helper {
    storageGet(key, def) {
        var out = def;
        if (localStorage[key] !== undefined) {
            out = JSON.parse(localStorage[key]);
        }

        return out;
    }

    storageSet(key, value) {
        localStorage[key] = JSON.stringify(value);
    }

    storageDel(key) {
        localStorage.removeItem(key);
    }

    getTimestamp() {
        return (new Date()).getTime();
    }

    getNowTimestamp() {
        return Number((Date.now() / 1000).toFixed(0));
    }

    // string Date to format '2021-05-26'
    getDateFromString(stringDate) {
        let dateObj = new Date(Date.parse(stringDate));
        return dateObj.getFullYear().toString()
            + "-"
            + (
                (dateObj.getMonth() + 1).toString().length === 2 ?
                    (dateObj.getMonth() + 1).toString() :
                    "0" + (dateObj.getMonth() + 1).toString())
            + "-"
            + (
                dateObj.getDate().toString().length === 2 ?
                    dateObj.getDate().toString() :
                    "0" + dateObj.getDate().toString());
    }

    parseFloats(val = '') {

        const r = /[+-]?([0-9]*[.])?[0-9]+/g;

        let float = val.toString().replace(',', '.').replace(/&nbsp;/gi, '').replace(/\s/g, '')
            .trim().match(r).map((v) => parseFloat(v));

        return float[0]
    }

    cleanLink(url) {
        if (url.indexOf('http') === -1) {
            url = 'https:'.concat(url);
        }
        return url.split('?')[0];
    }

    isAuth(store) {
        return store.getState().user.token.length > 0;
    }

    imgFullPath(origin, path) {
        if (path === '') return path;
        if (typeof path !== 'string') return '';

        const urlDataImg = new UrlParse(path);
        let imgPath = path;
        if (urlDataImg.host === null || urlDataImg.host === '') {
            imgPath = origin + path;
        } else if (urlDataImg.protocol === null || typeof path === 'string' && path.toString().substring(0, 4) !== 'http' || urlDataImg.protocol === '' || urlDataImg.protocol === 'chrome-extension:') {
            if (path.indexOf('//') > -1) {
                imgPath = (new UrlParse(origin)).protocol + path;
            } else {
                imgPath = origin + urlDataImg.pathname;
            }
        } else {
            imgPath = path;
        }
        imgPath.replace('chrome-extension://', (new UrlParse(origin)).protocol.concat('://'));

        return imgPath;
    }

    capitalize(s) {
        return s && s[0].toUpperCase() + s.slice(1);
    }

    getUrlInfo(url) {
        if (url === undefined) {
            return '';
        }
        if (url.indexOf('http') === -1) {
            url = 'http://' + url;
        }
        var result = {domain: '', subDomain: '', query: {}, pathname: ""};

        url = new URL(url);
        result.query = this.URLToArray(url.search);
        result.pathname = url.pathname;

        if (!url.hostname.match(new RegExp('^(?:([^:\\/?]+):)?(?:\\/\\/([^\\/]*))?([^?]*)(?:\\?([^$]*))?'))) {
            return result;
        }

        var matches = url.hostname.match(/((?:[^.]+)\.(?:(?:com?|org)\.)?\w+)$/i);
        if (matches) {
            result.domain = matches[1];
        }

        result.subDomain = url.hostname.replace(result.domain, '').replace(/\.$/, '');


        return result;
    }

    URLToArray(url) {
        var request = {}, key = "", value = "", str = "";
        var pairs = url.substring(url.indexOf('?') + 1).split('&');
        for (var i = 0; i < pairs.length; i++) {
            if (!pairs[i])
                continue;
            var pair = pairs[i].split('=');
            key = decodeURIComponent(pair[0]);
            str = pair[1];
            try {
                value = decodeURIComponent(pair[1]);
            } catch (err) {
                value = (pair[1]);
            }
            request[key] = value;
        }
        return request;
    }

    domainParts(url, count = 1) {
        if (url === undefined) {
            return '';
        }
        if (url.indexOf('http') === -1) {
            url = 'http://' + url;
        }
        var result = {domain: '', subDomain: '', query: {}};

        try {
            url = new URL(url);
        } catch (e) {
            return '';
        }

        result.query = this.URLToArray(url.search);

        if (!url.hostname.match(new RegExp('^(?:([^:\\/?]+):)?(?:\\/\\/([^\\/]*))?([^?]*)(?:\\?([^$]*))?'))) {
            return result;
        }

        var domainParts = url.hostname.split('.');
        var length = domainParts.length;

        var domainKey = [];

        if (length < count) {
            return url.hostname;
        }

        for (var i = length - count - 1; i < length; i++) {
            if (domainParts[i] === undefined) return undefined;
            domainKey.push(domainParts[i]);
        }
        return domainKey.join('.');
    }

    URLToArray(url) {
        var request = {}, key = "", value = "", str = "";
        var pairs = url.substring(url.indexOf('?') + 1).split('&');
        for (var i = 0; i < pairs.length; i++) {
            if (!pairs[i])
                continue;
            var pair = pairs[i].split('=');
            key = decodeURIComponent(pair[0]);
            str = pair[1];
            try {
                value = decodeURIComponent(pair[1]);
            } catch (err) {
                value = (pair[1]);
            }
            request[key] = value;
        }
        return request;
    }

    shareLink(socialName, url, title = '', description = '', img = '', linkName = 'ссылкой для друга', lang = 'ru') {
        url += url.indexOf('?') > -1 ? '&' : "?".concat("lang=").concat(lang);
        url = encodeURIComponent(url);

        let noEncDescription = description;
        description = encodeURIComponent(description);
        title = encodeURIComponent(title);
        img = encodeURIComponent(img);

        switch (socialName) {
            case 'vk':
                return "https://vk.com/share.php?redir=1&url=" + url + ((img !== '') ? "&image=" + img.toString() : "") + "&noparse=true" + "&title=" + title + "&comment=" + noEncDescription;
            case 'fb':
                return "http://www.facebook.com/sharer.php?u=" + url + "&description=" + description + ((img !== '') ? "&picture=" + img.toString() : "") + "&title=" + title;
            case 'ok':
                return "https://connect.ok.ru/dk?st.cmd=WidgetSharePreview&service=odnoklassniki&st.title=" + title + "&st.description=" + description + "&st.shareUrl=" + url + "&st.imageUrl=" + img;
            case "twitter":
                return "https://twitter.com/share?url=" + url + "&text=" + title + " " + description + "&hashtags=megabonus,кэшбэк,cashback&related=megabonuscom";
            default:
                return "https://megabonus.com"
        }
    }

    isActiveCashback(offerData) {
        // console.log('checkQueueForRedirect . isActiveCashback',offerData !== undefined , offerData.id > 0 , offerData.activeCash !== undefined , offerData.activeCash, this.getNowTimestamp(),offerData.activeCash > this.getNowTimestamp())
        return offerData !== undefined && offerData.id > 0 && offerData.activeCash !== undefined && offerData.activeCash > this.getNowTimestamp()
    }

    isNeedToShopToplineActivated(offerData) {
        return offerData !== undefined && offerData.showToplineActivated !== undefined && offerData.showToplineActivated;
    }

    getMinMoneyForLevel(level) {
        switch (level) {
            case 2:
                return 1;
            case 3:
                return 2;
            case 4:
                return 5;
            case 5:
                return 21;
            case 6:
                return 144;
            case 7:
                return 2584;
            default:
                return 1;
        }
    }

    declOfNum(number, titles) {
        var cases = [2, 0, 1, 1, 1, 2];
        return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
    }

    isAliepress(url) {
        return url.indexOf('aliexpress.') > -1;
    }

    isEnabledInExtension(offerData) {
        return Number(offerData.load_to_ext) === 1;
    }

    /**
     * Проверка на возможность проверки товара Aliexpress на аффилярность
     */
    isEnabledCheckAffiliate() {
        return chromeLib.storageGet(CHECK_AFFILIATE_ON, 1) > 0;
    }

    getUrlRedirect(offer, deepLink, component = '') {
        if (offer === undefined || offer.aff_link === undefined)
            return undefined;
        let deepLinkSplit = deepLink.split('?')[0];
        if (offer.aff_link.indexOf(offer.end_http) > -1) {
            return offer.aff_link.replace(offer.end_http, deepLinkSplit);
        }
        if (component.length > 0) {
            offer.aff_link += '&Component=' + component;
        }
        return offer.aff_link + '&Deeplink=' + deepLinkSplit;
    }

    getUrlToSmartcartTransitPage(offer, deepLink) {
        let smartcartTransitPage = 'https://smartcart.' + HOST + '/finditem?com=ExternalLink';
        return smartcartTransitPage + '&link=' + encodeURIComponent(this.getUrlRedirect(offer, deepLink));
    }

    getUrlToTransitPage(offer, component, deepLink) {
        // https://devmegabonus.com/transit/149?com=shop
        let transitPage = 'https://' + HOST + '/transit/' + offer.id;
        return transitPage + '?al=' + encodeURIComponent(this.getUrlRedirect(offer, deepLink, component));
    }

    createLinkWithUtm(link = 'https://megabonus.com', source = 'unknown', content = 'unknown') {
        let utm, hash = '';
        if (link.indexOf('#') > -1) {
            var arr = link.split('#');
            utm = arr[0];
            hash = arr[1];
        } else {
            utm = link;
        }
        if (utm.indexOf('?') > -1) {
            utm += utm.slice(-1) !== '?' ? '&' : '';
        } else {
            utm += '?';
        }
        let utm_props = {}
        utm_props.utm_source = chromeLib.detectBrowser();
        utm_props.utm_medium = 'ext';
        utm_props.utm_campaign = source;
        utm_props.utm_content = content;
        utm_props.utm_term = this.isActiveCashback(store.getState().activeTabOffer) ? 'activated' : 'notactivated';
        utm_props.utm_term += '|' + (store.getState().activeTabItemData.checkAffiliateStatus > 0 ? 'affiliate' : 'notaffiliate');
        if (store.getState().activeTabOffer.id > 0) {
            utm_props.utm_term += '|' + (store.getState().activeTabOffer.load_to_ext === "1" ? 'supportedshoppage' : 'notsupportedshoppage');
        } else {
            utm_props.utm_term += '|' + 'notshoppage';
        }
        // utm += Object.entries(utm_props).join('&').replaceAll(',','=') // 'replaceAll' Available only from Chrome 85
        utm += Object.entries(utm_props).join('&').replace(/,/gi, '=');

        if (hash.length > 0) {
            utm += '#' + hash;
        }
        return utm;
    }

    async cacheData(key, ttl, callback = async () => {
    }) {
        var data;
        key = 'cache_'.concat(key);
        if (lscache.get(key) !== null) {
            data = lscache.get(key);
            // console.warn('cacheData', 1, data)
        } else {
            lscache.set(key, data = await callback(), ttl)

            // console.warn('cacheData', 2, data)
        }
        return data
    }

    clickActivateCashback(offer, redirectType, tab) {
        // console.log(offer, redirectType, tab);
        let url = tab.url ? tab.url : tab.pendingUrl;

        var redirectUrl = helper.getUrlRedirect(offer, url);
        if (redirectUrl === undefined) {
            chromeLib.reloadTab(tab);
            return;
        }

        store.dispatch({
            type: SET_OFFER_ACTIVE,
            url: url,
            value: helper.getNowTimestamp() + OFFER_ACTIVE_SECONDS_LEAVE
        });

        redirectUrl = helper.addComponentToLink(redirectUrl, redirectType);
        chromeLib.redirectTab(tab, redirectUrl);
    }

    addComponentToLink(link, component) {
        return link += "&Component=" + component;
    }

    /**
     * get user friendly currency symbol
     * @param currency
     */
    getCurrencySymbol(currency) {

        switch (currency) {
            case "USD":
                return "$";
            case "RUB":
            case "RUR":
                return "₽";
            default:
                return currency;
        }
    }

    /**
     * get user friendly price
     *
     * @param price
     * @param currency
     * @returns {string|*}
     */
    getUserFriendlyPrice(price, currency) {

        let curr = this.getCurrencySymbol(currency);

        switch (curr) {
            case "$":
                return curr + price;

            default:
                return price + curr;
        }
    }

    /**
     * check is num is odd
     * @param num
     * @returns {number}
     */
    isOdd(num) {
        return num % 2;
    }
}

const helper = new Helper();
export default helper
