var md5 = require('md5');
class Cache {
    constructor() {
        this.dataObj = {};
    }

    set(key, value) {
        if (!chromeLib.isChrome()) {
            return this.dataObj[key] = value;
        }
        return chromeLib.storageSet(key, value);
    }

    get(key, defaultValue) {
        var cache;
        if (!chromeLib.isChrome()) {
            cache = (this.dataObj[key] !== undefined) ? this.dataObj[key] : defaultValue;
        } else {
            cache = chromeLib.storageGet(key, defaultValue);
        }

        if (typeof cache === 'object') cache.cache = true;
        return cache;
    }

    isCache(data) {
        return (typeof cache === 'object' && typeof data.cache !== 'undefined');
    }

    getStorageMethodApiKey(methodName, data) {
        return 'cache_' .concat(md5(methodName.split('/')[0] + '?' + JSON.stringify(data)));
    }
}

const cache = new Cache;
export default cache;