import { ADD_URL_TO_QUEUE, DELETE_TAB_HISTORY, HOST, PRINT_REDUCER_EVENT } from '../constant';

/**
 * Возвращает очередь редиректов по вкладке
 * @param store
 * @param tab
 * @param length
 * @returns {string[]|Array}
 */
export function getUrlQueue (store, tab, length = 0) {
  var tabId = tab.id;
  length = length < 2 ? 2 : length;

  var defUrl = 'https://' + HOST;
  var queueLength = 0;
  if (store[tabId] === undefined) {
    return [
      defUrl,
      defUrl
    ];
  }

  queueLength = store[tabId].length;

  var returnArr = [];

  if (queueLength - length < 0) {
    var i;
    for (i = 0; i < length - queueLength; i++) {
      returnArr.push(defUrl);
    }
    for (var index in store[tabId]) {
      returnArr.push(store[tabId][index]);
    }
  } else if (queueLength - length == 0) {
    for (var index in store[tabId]) {
      returnArr.push(store[tabId][index]);
    }
  } else if (queueLength - length > 0) {
    for (var index in store[tabId]) {
      if (index > (queueLength - length - 1)) {
        returnArr.push(store[tabId][index]);
      }
    }
    ;
  }
  return returnArr;
}


export default function optionsReducer (state = {}, action) {
  switch (action.type) {
    /**
     * Добавить url в очередь редиректов
     */
    case ADD_URL_TO_QUEUE:
      var obj = {
        ...state
      };
      var url = action.url.toString().replace('aliexpress.ru', 'aliexpress.com');

      if (obj[action.tab.id] === undefined) {
        obj[action.tab.id] = [];

        obj[action.tab.id].push(url);
      }

      if (obj[action.tab.id][obj[action.tab.id].length - 1] != url) {
        obj[action.tab.id].push(url);
      }

      if (obj[action.tab.id].length > 15) {
        obj[action.tab.id].shift();
      }

      return obj;

    /**
     * Удалить очередь редиректов по TabID
     */
    case DELETE_TAB_HISTORY:
      var obj = {
        ...state
      };
      if (obj[action.tabId] !== undefined) {
        delete obj[action.tabId];

      }
      return obj;

    default:
      return {
        ...state
      };
  }
}
