import { SET_SA_ITEMS, REMOVE_SA_ITEM, ADD_SA_ITEMS, PRINT_REDUCER_EVENT } from '../constant';

export default function saleAlertLinksReducer (state = {
  links: {},
}, action) {
  switch (action.type) {
    case ADD_SA_ITEMS:
      return {
        links: {
          ...state.links,
          ...action.linkObj
        }
      };
    case SET_SA_ITEMS:
      return {
        links: action.value || {},
      };
    case REMOVE_SA_ITEM:
      let data = state.links;
      delete data[action.link];
      return {
        links: data,
      };
    default:
      return {
        ...state
      };
  }
}
